import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';

import { ConfirmationService } from 'primeng/api';
import { FieldDefinition } from '../field-definition';
import { ItemModel } from '../../../../dao/item-model';

@Component({
  selector: 'app-dynamic-form',
  templateUrl: './dynamic-form.component.html',
  styleUrls: ['./dynamic-form.component.scss']
})
export class DynamicFormComponent implements OnChanges, OnInit {

  @Input() vm: ItemModel;
  @Input() vmDefinition: Array<FieldDefinition>;
  @Input() errorMessage: string;
  @Output() cancel: EventEmitter<any> = new EventEmitter();
  @Input() hasSaveButton = true;
  @Output() save: EventEmitter<any> = new EventEmitter();
  @Input() extraButtons: Array<any>;
  @Output() extraAction: EventEmitter<any> = new EventEmitter();

  form: FormGroup;
  submitted = false;
  vmCopy: ItemModel;

  constructor(private confirmationService: ConfirmationService) { }

  resetForm() {
    const group = {};
    this.vmCopy = Object.assign({}, this.vm);
    this.vmDefinition.forEach(field => {
      group[field.key] = field.required ?
        new FormControl({value: this.vmCopy[field.key], disabled: field.disabled}, Validators.required) :
        new FormControl({value: this.vmCopy[field.key], disabled: field.disabled});
    });
    this.form = new FormGroup(group);
  }

  ngOnChanges(changes: SimpleChanges) {
    this.resetForm();
  }

  ngOnInit() {
    // this.resetForm();
  }

  onCancel() {
    if (this.form.pristine) {
      this.errorMessage = null;
      this.cancel.emit();
    } else {
      this.confirmationService.confirm({
        message: 'Unsaved changes will be lost! Are you sure that you want to proceed?',
        header: 'Confirmation',
        icon: 'fa fa-question-circle',
        accept: () => {
          this.errorMessage = null;
          this.cancel.emit();
        }
      });
    }
  }

  onSave() {
    this.submitted = true;
    if (this.form.valid) {
      this.save.emit(this.form.value);
    }
  }

  extraActionClick(action: string) {
    this.extraAction.emit(action);
  }
}



