class FidReaction {
  id: number;
  name: string;
}

export class FidReactions {
  public static fidReactions: FidReaction[] = [
    {id: 1, name: 'Engine stop (direct)'},
    {id: 2, name: 'Engine stop (indirect)'},
    {id: 3, name: 'Yes (OBD)'},
    {id: 4, name: 'Yes (technical)'},
  ];

  static getFidReactionById(id: number): FidReaction {
    return FidReactions.fidReactions.find(fidReaction => fidReaction.id === id);
  }
}



