import { Component, OnInit } from '@angular/core';

import { DashboardItem } from '../../common/components/dashboard-item/dashboard-item';
import { AccountService } from '../../services/account.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {

  dashboardItems: DashboardItem[] = [
    {
      label: 'Create',
      description: 'Create',
      href: '/create',
      hrefLabel: 'View details ',
      icons: ['fa-file-text-o'],
      // disabled: true,
    },
    // {
    //   label: 'Create Report',
    //   description: 'Create Report',
    //   href: '/create-report',
    //   hrefLabel: 'View details ',
    //   icons: ['fa-file-text-o'],
    // },
  ];

  constructor(private accountService: AccountService) { }

  ngOnInit() {
    this.accountService.getUserInfo()
      .subscribe(
        result => {
          if (result.roles[0] === 'Admin') {
            this.dashboardItems.push(
                {
                  label: 'Projects',
                  description: 'View projects list',
                  href: '/projects',
                  hrefLabel: 'View details ',
                  icons: ['fa-list-alt'],
                },
                {
                  label: 'Failures',
                  description: 'View failures list',
                  href: '/failures',
                  hrefLabel: 'View details ',
                  icons: ['fa-warning'],
                },
                {
                  label: 'Failures - Projects',
                  description: 'View a failures list with the possibility to assign/unassign them to/from projects',
                  href: '/failures-projects',
                  hrefLabel: 'View details ',
                  icons: ['fa-warning', 'fa-list-alt'],
                }
            );
          }
        },
      );
  }

}



