import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { RoutingPaths } from '../../utils/routing-paths';
import { LabelDescription } from '../../dao/label-description';
import { LabelDescriptionsService } from '../../services/label-descriptions.service';
import { FieldDefinition } from '../../common/components/dynamic-forms/field-definition';
import { LabelDescriptionFieldMapper } from '../../dao/label-description-field-mapper';

@Component({
  selector: 'app-label-description-detail',
  templateUrl: './label-description-detail.component.html',
  styleUrls: ['./label-description-detail.component.scss']
})
export class LabelDescriptionDetailComponent implements OnInit {
  private isNew: boolean;
  item: LabelDescription;
  itemDefinition: Array<FieldDefinition> = [
    {
      key: this.fieldMapper.fieldMap['id'],
      label: 'Id',
      type: 'number',
      hidden: true,
      disabled: true,
      isId: true,
      required: true,
    },
    {
      key: this.fieldMapper.fieldMap['name'],
      label: 'Name',
      type: 'text',
      disabled: true,
      required: true,
    },
    {
      key: this.fieldMapper.fieldMap['unit'],
      label: 'Unit',
      type: 'text',
      disabled: true,
      required: true,
    },
    {
      key: this.fieldMapper.fieldMap['description'],
      label: 'Description',
      type: 'text',
      disabled: true,
      required: true,
    },
    {
      key: this.fieldMapper.fieldMap['projectId'],
      label: 'Used in Project',
      type: 'number',
      disabled: true,
    },
    {
      key: this.fieldMapper.fieldMap['comment'],
      label: 'Comment',
      type: 'textarea',
      disabled: true,
    },
  ];
  errorMessage: string;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private dataService: LabelDescriptionsService,
    private fieldMapper: LabelDescriptionFieldMapper
  ) { }

  ngOnInit() {
    this.isNew = this.route.snapshot.url[1].path === 'new';

    if (this.isNew) {
      this.item = {
        id: 0,
        name: null,
        unit: null,
        description: null,
        projectId: null,
        comment: null,
      };
    } else {
      this.dataService.getItem(this.route.snapshot.params['id'])
        .subscribe((item: LabelDescription) => {
          this.item = item;
        }
      );
    }
  }

  private cancel() {
    this.router.navigate([RoutingPaths.labelDescriptions]);
  }

  private save(item: LabelDescription) {
    // TODO: API endpoints?
    // if (this.isNew) {
    //   this.create(item);
    // }
    // else {
    //   this.update(item);
    // }

    // this.cancel();
  }

  private update(item: LabelDescription) {
    // TODO: API endpoint?
    // this.dataService.editItem(item.id, item)
    //   .subscribe((x: any) => {
    //     console.log("label description " + item.id + " updated");
    //   }
    // );
  }

  private create(item: LabelDescription) {
    // TODO: API endpoint?
    // this.dataService.addItem(item)
    //   .subscribe((x: any) => {
    //     console.log("label description " + x.id + " created");
    //   }
    // );
  }
}



