import { Injectable } from '@angular/core';

import { Role } from '../dao/role';
import { ApisService } from '../common/services/apis.service';
import { RoleFieldMapper } from '../dao/role-field-mapper';
import { CrudService } from '../common/services/crud.service';

@Injectable()
export class RolesService extends CrudService<Role> {
  constructor(apisService: ApisService, fieldMapper: RoleFieldMapper) {
    super(apisService, fieldMapper);
  }

  get apiRoot(): string {
    return 'Roles';
  }
}



