import { Component, ViewChild } from '@angular/core';
import { AdvancedTableData } from '../../common/components/advanced-table-data';
import { LabelDescriptionsService } from '../../services/label-descriptions.service';
import { LabelDescription } from '../../dao/label-description';
import { LabelDescriptionsDataSource } from './label-descriptions.datasource';
import { AdvancedTableComponent } from '../../common/components/advanced-table/advanced-table.component';
import { Router } from '@angular/router';
import { RoutingPaths } from '../../utils/routing-paths';
import { LabelDescriptionFieldMapper } from '../../dao/label-description-field-mapper';

@Component({
  selector: 'app-label-descriptions',
  templateUrl: './label-descriptions.component.html',
  styleUrls: ['./label-descriptions.component.scss']
})
export class LabelDescriptionsComponent {
  listTitle = 'Label Descriptions';
  @ViewChild('table') table: AdvancedTableComponent;
  advancedTableData: AdvancedTableData<LabelDescription> = {
    columns: [
      {
        description: 'Label Name',
        valueGetter: item => item.name,
        matColumnDefValue: this.fieldMapper.fieldMap['name']
      },
      {
        description: 'Unit',
        valueGetter: item => item.unit,
        matColumnDefValue: this.fieldMapper.fieldMap['unit']
      },
      {
        description: 'Description',
        valueGetter: item => item.description,
        matColumnDefValue: this.fieldMapper.fieldMap['description']
      },
      {
        description: 'Used in project',
        valueGetter: item => String(item.projectId),
        matColumnDefValue: this.fieldMapper.fieldMap['projectId']
      },
      {
        description: 'Comment',
        valueGetter: item => item.comment,
        matColumnDefValue: this.fieldMapper.fieldMap['comment']
      }
    ],
    displayedColumns: [
      this.fieldMapper.fieldMap['name'],
      this.fieldMapper.fieldMap['unit'],
      this.fieldMapper.fieldMap['description'],
      this.fieldMapper.fieldMap['projectId'],
      this.fieldMapper.fieldMap['comment']
    ],
    dataSource: new LabelDescriptionsDataSource(this.itemsManagementService),
    actions: [
      {
        icon: 'edit',
        disabled: any => false,
        event: x => this.onEdit(x)
      }
    ]
  };

  constructor(
    private router: Router,
    private itemsManagementService: LabelDescriptionsService,
    private fieldMapper: LabelDescriptionFieldMapper
  ) {}

  onEdit(row: LabelDescription): void {
    this.router.navigate([RoutingPaths.labelDescriptions, row.id]);
  }

  onDelete(row: LabelDescription): void {
    this.itemsManagementService.removeItem(row.id).subscribe(result => {
      this.table.items.splice(this.table.items.indexOf(row), 1);
    });
  }

  private addItem(): void {
    this.router.navigate([RoutingPaths.labelDescriptions, 'new']);
  }
}



