import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { RoutingPaths } from '../../utils/routing-paths';
import { ChannelDescription } from '../../dao/channel-description';
import { ChannelDescriptionsService } from '../../services/channel-descriptions.service';
import { FieldDefinition } from '../../common/components/dynamic-forms/field-definition';
import { ChannelDescriptionFieldMapper } from '../../dao/channel-description-field-mapper';
import { CanComponentDeactivate } from '../../guards/can-deactivate.guard';

@Component({
  selector: 'app-channel-description-detail',
  templateUrl: './channel-description-detail.component.html',
  styleUrls: ['./channel-description-detail.component.scss']
})
export class ChannelDescriptionDetailComponent implements OnInit, CanComponentDeactivate {
  private isNew: boolean;
  item: ChannelDescription;
  itemDefinition: Array<FieldDefinition> = [
    {
      key: this.fieldMapper.fieldMap['id'],
      label: 'Id',
      type: 'number',
      hidden: true,
      isId: true,
      required: true,
    },
    {
      key: this.fieldMapper.fieldMap['status'],
      label: '',
      type: 'label',
    },
    {
      key: this.fieldMapper.fieldMap['name'],
      label: 'Name',
      type: 'text',
      required: true,
    },
    {
      key: this.fieldMapper.fieldMap['description'],
      label: 'Description',
      type: 'text',
    },
    {
      key: this.fieldMapper.fieldMap['boschDescription'],
      label: 'Bosch Description',
      type: 'text',
    },
    {
      key: this.fieldMapper.fieldMap['comment'],
      label: 'Comment',
      type: 'textarea',
    },
    {
      key: this.fieldMapper.fieldMap['projectCode'],
      label: 'Project Code',
      type: 'text',
    },
    {
      key: this.fieldMapper.fieldMap['projectId'],
      label: 'Used in Project',
      type: 'number',
    },
  ];
  errorMessage: string;
  showDialog = false;
  dialogMessage: string;
  protected lockedByCurrentUser = false;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private dataService: ChannelDescriptionsService,
    private fieldMapper: ChannelDescriptionFieldMapper
  ) { }

  ngOnInit() {
    this.isNew = this.route.snapshot.url[1].path === 'new';

    if (this.isNew) {
      this.lockedByCurrentUser = true;
      this.item = {
        id: 0,
        name: null,
        description: null,
        boschDescription: null,
        comment: null,
        projectCode: null,
        projectId: null,
      };
    } else {
      const id = this.route.snapshot.params['id'];
      const statusIndex = this.itemDefinition.findIndex(item => item.key === this.fieldMapper.fieldMap['status']);
      this.dataService.lockItem(id).subscribe(
        success => {
          this.lockedByCurrentUser = true;
          this.itemDefinition[statusIndex].label = 'Locked by you';
          this.getItem();
        },
        error => {
          if (error.status === 409) { // already locked
            this.lockedByCurrentUser = false;
            this.itemDefinition[statusIndex].label = 'Locked by another user';
            this.getItem();
          }
        },
      );
    }
  }

  private getItem() {
    this.dataService.getItem(this.route.snapshot.params['id'])
      .subscribe((item: ChannelDescription) => {
        this.item = item;
      }
    );
  }

  private cancel() {
    this.router.navigate([RoutingPaths.channelDescriptions]);
  }

  private save(item: ChannelDescription) {
    if (this.isNew) {
      this.create(item);
    } else {
      this.update(item);
    }

    this.cancel();
  }

  private update(item: ChannelDescription) {
    this.dataService.editItem(item.id, item)
      .subscribe((x: any) => {
        console.log('channel description ' + item.id + ' updated');
      }
    );
  }

  private create(item: ChannelDescription) {
    // TODO: ERROR (No 'Access-Control-Allow-Origin' header is present on the requested resource)
    this.dataService.addItem(item)
      .subscribe((x: any) => {
        console.log('channel description ' + x.id + ' created');
      }
    );
  }

  dialogKeepLock() {
    this.showDialog = false;
    this.lockedByCurrentUser = false;
    this.cancel();
  }

  dialogUnlock() {
    this.showDialog = false;
    this.dataService.unlockItem(this.route.snapshot.params['id']).subscribe(
      success => {
        this.lockedByCurrentUser = false;
        this.cancel();
      },
      error => {
        console.log('error unlocking');
      },
    );
  }

  canDeactivate(): boolean {
    if (!this.lockedByCurrentUser) {
      return true;
    }
    this.dialogMessage = 'You have locked this channel description, which prevents other users from editing it! What would you like to do?';
    this.showDialog = true;
  }
}



