import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { RoutingPaths } from '../../utils/routing-paths';
import { User } from '../../dao/user';
import { UsersService } from '../../services/users.service';
import { FieldDefinition, SelectOption } from '../../common/components/dynamic-forms/field-definition';
import { UserFieldMapper } from '../../dao/user-field-mapper';
import { RolesService } from '../../services/roles.service';
import { RoleFieldMapper } from '../../dao/role-field-mapper';
import { Role } from '../../dao/role';

@Component({
  selector: 'app-user-detail',
  templateUrl: './user-detail.component.html',
  styleUrls: ['./user-detail.component.scss']
})
export class UserDetailComponent implements OnInit {

  private roles: SelectOption[] = [];

  item: User;
  itemDefinition: Array<FieldDefinition> = [
    {
      key: this.fieldMapper.fieldMap.id,
      label: 'Id',
      type: 'number',
      hidden: true,
      disabled: true,
      isId: true,
      required: true,
    },
    {
      key: this.fieldMapper.fieldMap.username,
      label: 'Username',
      type: 'text',
      disabled: true,
      required: true,
    },
    {
      key: this.fieldMapper.fieldMap.roleId,
      label: 'Role',
      type: 'select',
      disabled: false,
      required: true,
      options: [],
      selectedOptions: [],
    },
  ];
  errorMessage: string;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private dataService: UsersService,
    private fieldMapper: UserFieldMapper,
    private rolesDataService: RolesService,
    private rolesFieldMapper: RoleFieldMapper
  ) { }

  ngOnInit() {
    const id = this.route.snapshot.params['id'];
    const userInfo = JSON.parse(localStorage.getItem('user'))._value;
    if (userInfo.id === id) {
      alert('You cannot edit your own role!');
      this.cancel();
    }

    // get roles from api
    this.rolesDataService.getItems([], this.rolesFieldMapper.fieldMap.name, 'asc', 0, 0)
      .subscribe(result => {
        this.roles = result.data.map(
          (role: Role) => {
            return {label: role[this.rolesFieldMapper.fieldMap.name], value: role[this.rolesFieldMapper.fieldMap.id]};
          }
        );

        const rolesFieldDefinitionIdx = this.itemDefinition.findIndex(item => item.key === this.fieldMapper.fieldMap.roleId);
        this.itemDefinition[rolesFieldDefinitionIdx].options = this.roles;

        // get user info
        this.dataService.getItem(id)
          .subscribe((item: User) => {
            this.item = item;
            this.itemDefinition[rolesFieldDefinitionIdx].selectedOptions = [this.item.roleId.toString()];
          }
        );
      }
    );
  }

  private cancel() {
    this.router.navigate([RoutingPaths.users]);
  }

  private save(item: User) {
    this.update(item);
  }

  private update(item: User) {
    const newRoleId = typeof item.roleId === 'number' ? item.roleId : item.roleId[0];

    // update only if selected role is different from the current one
    if (newRoleId !== this.item.roleId) {
      this.dataService.editItem(this.item.id, item)
      .subscribe((x: any) => {
          console.log('userId #' + this.item.id + ' updated with roleId #' + newRoleId);
        },
        error => {},
        () => {
          this.cancel();
        }
      );
    } else {
      alert('Nothing to update! Selected role is equal to current role!');
    }
  }
}



