import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';

import { ApisService } from './apis.service';
import { IDetailsService } from '../components/details-service';
import { AdvancedTableColumnFilter } from '../components/advanced-table-data';
import { ItemModelFieldMapper } from '../../dao/item-model-field-mapper';

@Injectable()
export abstract class CrudService<T extends object> implements IDetailsService {

  constructor(protected apisService: ApisService, protected fieldMapper: ItemModelFieldMapper) {}

  getItems(
    filter: AdvancedTableColumnFilter[],
    sortField: string,
    sortDirection: string,
    pageIndex: number,
    pageSize: number,
    fields?: string[]
  ): Observable<ListResults<T>> {
    let url: string = this.baseApiUrlWithRoot;
    let setFirstParam = true;

    if (fields != null && fields.length > 0) {
      url += '?fields=' + fields.join(',');
      setFirstParam = false;
    }

    if (sortField.length > 0 && sortDirection.length > 0) {
      const apiSortField = this.getApiFieldName(sortField);
      if (apiSortField != null && apiSortField.length > 0) {
        url += setFirstParam ? '?' : '&';
        url += 'sortField=' + apiSortField + '&sortDirection=' + sortDirection;
      }
    }

    return this.apisService
      .performGet(url, true)
      .map((returningItems: T[]) => {
        if (filter.length > 0) {
          returningItems = returningItems.filter(x =>
            this.isNotFiltered(x, filter)
          );
        }
        const length = returningItems.length;
        if (pageIndex > 0 && pageSize > 0) {
          returningItems = returningItems.slice(
            pageIndex * pageSize,
            (pageIndex + 1) * pageSize
          );
        }

        return { data: returningItems, length: length };
      });
  }

  getItem(id: number): Observable<T> {
    return this.apisService
      .performGet(
        this.baseApiUrlWithRoot + '/' + id,
        true
      )
      .map((returningItem: T) => returningItem);
  }

  removeItem(id: number): Observable<object> {
    return this.apisService.performDelete(
      this.baseApiUrlWithRoot + '/' + id,
      true
    );
  }

  addItem(item: T): Observable<object> {
    return this.apisService.performPost(
      this.baseApiUrlWithRoot,
      item,
      true
    );
  }

  editItem(id: number, item: T): Observable<object> {
    return this.apisService.performPut(
      this.baseApiUrlWithRoot + '/' + id,
      item,
      true
    );
  }

  isNotFiltered(item: T, filters: AdvancedTableColumnFilter[]): boolean {
    let match = true;

    for (const filter of filters) {
      if (item[filter['field']] === null) {
        return false;
      }

      switch (filter['matchMode']) {
        case 'equals':
          match = match && (item[filter['field']].toString().toLowerCase() === filter['value'].toString().toLowerCase());
          break;
        case 'contains':
          match = match && (item[filter['field']].toString().toLowerCase().indexOf(filter['value'].toString().toLowerCase()) !== -1);
          break;
        case 'startsWith':
          match = match && (item[filter['field']].toString().toLowerCase().indexOf(filter['value'].toString().toLowerCase()) === 0);
          break;
        case 'notStartsWith':
          match = match && (item[filter['field']].toString().toLowerCase().indexOf(filter['value'].toString().toLowerCase()) !== 0);
          break;
        case 'in':
          match = match && true;
          break;
      }
    }

    return match;
  }

  get baseApiUrlWithRoot(): string {
    return this.apisService.baseApiUrl + '/' + this.apiRoot;
  }

  getApiFieldName(field: string): string {
    return this.fieldMapper.fieldMap[field];
  }

  abstract get apiRoot(): string;
}

export class ListResults<T> {
  data: T[];
  length: number;
}



