import { Injectable } from '@angular/core';

import { CrudService } from './../common/services/crud.service';
import { ApisService } from '../common/services/apis.service';
import { LabelDescription } from '../dao/label-description';
import { LabelDescriptionFieldMapper } from '../dao/label-description-field-mapper';

@Injectable()
export class LabelDescriptionsService extends CrudService<LabelDescription> {
  constructor(apisService: ApisService, fieldMapper: LabelDescriptionFieldMapper) {
    super(apisService, fieldMapper);
  }

  get apiRoot(): string {
    return 'LabelDescriptions';
  }
}



