import { Component, Input, Output, EventEmitter } from '@angular/core';
import { saveAs } from 'file-saver';

@Component({
  selector: 'app-warnings',
  templateUrl: './warnings.component.html',
  styleUrls: ['./warnings.component.scss']
})
export class WarningsComponent {

  @Input() visibleIn = false;
  @Input() a2lWarnings: string[];
  @Input() panelTitle: any;
  @Output() visibleOut: EventEmitter<boolean> = new EventEmitter<boolean>();

  constructor() { }

  close() {
    this.visibleIn = false;
    this.visibleOut.emit(false);
  }

  saveWarningsFile() {
    const data = 'Project: ' + this.panelTitle['a2l'] + '\r\nDataset: ' + this.panelTitle['hex'] + '\r\n\r\nWARNINGS:\r\n\r\n';
    const blob = new Blob( [data + this.a2lWarnings.join('\r\n')], {type: 'text/plain;charset=utf-8'} );
    saveAs(
      blob,
      this.panelTitle['a2l'] && this.panelTitle['hex'] ?
      (
        this.panelTitle['a2l'].slice(0, this.panelTitle['a2l'].lastIndexOf('.')) +
        '_FnR_' +
        this.panelTitle['hex'].slice(0, this.panelTitle['hex'].lastIndexOf('.')) + '_warnings.txt'
      ) :
      'FnR_warnings.txt'
    );
  }
}



