    title: 'fnrFailureDetailTitle',
    dtc: 'fnrFailureDetailDtc',
    component: 'fnrFailureDetailComponent',
    obdSystem: 'fnrFailureDetailObdSystem',
    obdCode: 'fnrFailureDetailObdCode',
