import { Component, OnInit, OnChanges, SimpleChanges } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { FormGroup, Validators, FormControl } from '@angular/forms';

import { Project } from '../../dao/project';
import { ProjectsService } from '../../services/projects.service';
import { SelectOption } from '../../common/components/dynamic-forms/field-definition';
import { ProjectFieldMapper } from '../../dao/project-field-mapper';
import { LabelDescriptionCheckService, LabelDescriptionCheckRequest } from '../../services/label-description-check.service';
import { LabelDescriptionCheck } from '../../dao/label-description-check';
import { LabelDescriptionCheckFieldMapper } from '../../dao/label-description-check-field-mapper';

@Component({
  selector: 'app-check-labels-existence',
  templateUrl: './check-labels-existence.component.html',
  styleUrls: ['./check-labels-existence.component.scss']
})
export class CheckLabelsExistenceComponent implements OnInit, OnChanges {
  loading = false;
  selectedProject: number;
  projectOptions: SelectOption[];
  globalScope = false;
  form: FormGroup;
  tableFlags = [
    {id: 'generalRemark', label: 'General Remark', value: true},
    {id: 'testCondition', label: 'Test Condition', value: true},
    {id: 'faultDetection', label: 'Fault Detection', value: true},
    {id: 'defectDebouncing', label: 'Defect Debouncing', value: true},
    {id: 'faultHealing', label: 'Fault Healing', value: true},
    {id: 'healingDebouncing', label: 'Healing Debouncing', value: true},
    {id: 'systemReaction', label: 'System Reaction', value: true},
    {id: 'faultDisabling', label: 'Fault Disabling', value: true},
    {id: 'faultCause', label: 'Fault Cause', value: true},
    {id: 'faultRepair', label: 'Fault Repair', value: true},
  ];
  checkLabelsResult: LabelDescriptionCheck[] = [];
  cols: any[] = [
    {
      header: 'Failure',
      field: this.fieldMapper.fieldMap['failure'],
      filterMatchMode: 'contains'
    },
    {
      header: 'Label Name',
      field: this.fieldMapper.fieldMap['labelName'],
      filterMatchMode: 'contains'
    },
    {
      header: 'Table',
      field: this.fieldMapper.fieldMap['table'],
      filterMatchMode: 'contains'
    },
    {
      header: 'Warning',
      field: this.fieldMapper.fieldMap['warning'],
      filterMatchMode: 'contains'
    },
  ];

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private dataService: LabelDescriptionCheckService,
    private fieldMapper: LabelDescriptionCheckFieldMapper,
    private projectsDataService: ProjectsService,
    private projectsFieldMapper: ProjectFieldMapper
  ) { }

  ngOnInit() {
    this.projectOptions = [];
    this.projectsDataService
      .getItems([], this.projectsFieldMapper.fieldMap.name, 'asc', null, null)
      .subscribe(result => {
        result.data.map((project: Project) => {
          const formattedVersion = project.version.slice(0, 3) + '_' + (project.version.slice(3).length > 0 ?
            project.version.slice(3) :
            '0'
          );
          this.projectOptions.push({value: project.id, label: project.ecu + '_' + project.project + '_' + formattedVersion});
        });
        this.selectedProject = this.projectOptions[0].value;
      }
    );
    this.resetForm();
  }

  resetForm() {
    const group = {};
    group['globalScope'] = new FormControl(this.globalScope);
    group['selectedProject'] = new FormControl('selectedProject', Validators.required);
    for (const tableFlag of this.tableFlags) {
      group[tableFlag.id] = new FormControl(tableFlag.id);
    }
    this.form = new FormGroup(group);
  }

  ngOnChanges(changes: SimpleChanges) {
    this.resetForm();
  }

  private cancel() {
    // TODO: home or back?
    this.router.navigate(['home']);
  }

  private checkLabels() {
    this.loading = true;
    this.checkLabelsResult = [];

    this.dataService.check(this.getRequestParams(this.form.value)).subscribe(result => {
      this.checkLabelsResult = result;
      this.loading = false;
    });
  }

  private checkLabelsReport() {
    this.loading = true;
    this.checkLabelsResult = [];

    const a = document.createElement('a');
    a.hidden = true;
    document.body.appendChild(a);

    this.dataService.getTxtReport(this.getRequestParams(this.form.value)).subscribe(result => {
      const blob = new Blob([result], { type: 'text/plain' });
      a.href = window.URL.createObjectURL(blob);
      if (this.globalScope) {
        a.download = 'Global_Check_Report.txt';
      } else {
        const selectedProjectIdx = this.projectOptions.findIndex(item => item.value === this.selectedProject);
        a.download = this.projectOptions[selectedProjectIdx].label + '_WRN.txt';
      }
      a.click();

      const resultArray: string[] = result.split(/\r?\n/);
      if (!this.globalScope) {
        resultArray.shift();
      }
      for (const record of resultArray) {
        const cols: string[] = record.split(' - ');
        this.checkLabelsResult.push({
          failure: cols[0],
          labelName: cols[1],
          table: cols[2],
          warning: cols[3],
        });
      }

      // this.checkLabelsResult = result;
      this.loading = false;
    });
  }

  private getRequestParams(formValues: any): LabelDescriptionCheckRequest {
    const requestParams = new LabelDescriptionCheckRequest;
    requestParams.projectId = this.globalScope ? null : formValues.selectedProject;
    requestParams.generalRemark = formValues.generalRemark;
    requestParams.testCondition = formValues.testCondition;
    requestParams.faultDetection = formValues.faultDetection;
    requestParams.defectDebouncing = formValues.defectDebouncing;
    requestParams.faultHealing = formValues.faultHealing;
    requestParams.healingDebouncing = formValues.healingDebouncing;
    requestParams.systemReaction = formValues.systemReaction;
    requestParams.faultDisabling = formValues.faultDisabling;
    requestParams.faultCause = formValues.faultCause;
    requestParams.faultRepair = formValues.faultRepair;
    return requestParams;
  }
}



