import { Component, ViewChild } from '@angular/core';
import { Router } from '@angular/router';

import { AdvancedTableData } from '../../common/components/advanced-table-data';
import { ChannelDescriptionsService } from '../../services/channel-descriptions.service';
import { ChannelDescription } from '../../dao/channel-description';
import { ChannelDescriptionsDataSource } from './channel-descriptions.datasource';
import { AdvancedTableComponent } from '../../common/components/advanced-table/advanced-table.component';
import { RoutingPaths } from '../../utils/routing-paths';
import { ChannelDescriptionFieldMapper } from '../../dao/channel-description-field-mapper';

@Component({
  selector: 'app-channel-descriptions',
  templateUrl: './channel-descriptions.component.html',
  styleUrls: ['./channel-descriptions.component.scss']
})
export class ChannelDescriptionsComponent {
  listTitle = 'Channel Descriptions';
  @ViewChild('table') table: AdvancedTableComponent;
  advancedTableData: AdvancedTableData<ChannelDescription> = {
    columns: [
      {
        description: 'Channel Name',
        valueGetter: item => item.name,
        matColumnDefValue: this.fieldMapper.fieldMap['name']
      },
      {
        description: 'Description',
        valueGetter: item => item.description,
        matColumnDefValue: this.fieldMapper.fieldMap['description']
      },
      {
        description: 'Bosch Description',
        valueGetter: item => item.boschDescription,
        matColumnDefValue: this.fieldMapper.fieldMap['boschDescription']
      },
      {
        description: 'Used in Project',
        valueGetter: item => String(item.projectId),
        matColumnDefValue: this.fieldMapper.fieldMap['projectId']
      },
      {
        description: 'Comment',
        valueGetter: item => item.comment,
        matColumnDefValue: this.fieldMapper.fieldMap['comment']
      }
    ],
    displayedColumns: [
      this.fieldMapper.fieldMap['name'],
      this.fieldMapper.fieldMap['description'],
      this.fieldMapper.fieldMap['boschDescription'],
      this.fieldMapper.fieldMap['projectId'],
      this.fieldMapper.fieldMap['comment']
    ],
    dataSource: new ChannelDescriptionsDataSource(this.crudService),
    actions: [
      {
        icon: 'edit',
        disabled: any => false,
        event: x => this.onEdit(x)
      },
      {
        icon: 'trash',
        disabled: any => false,
        event: x =>
          this.onDelete(x)
      }
    ]
  };

  showDialog = false;
  dialogMessage: string;

  constructor(
    private router: Router,
    private crudService: ChannelDescriptionsService,
    private fieldMapper: ChannelDescriptionFieldMapper
  ) {}

  onEdit(row: ChannelDescription): void {
    this.router.navigate([RoutingPaths.channelDescriptions, row.id]);
  }

  onDelete(row: ChannelDescription): void {
    this.crudService.lockItem(row.id).subscribe(
      item => {
        // if record is successfully locked => proceed to delete
        this.crudService.removeItem(row.id).subscribe(result => {
          this.table.items.splice(this.table.items.indexOf(row), 1);
        });
      },
      error => {
        if (error.status === 409) { // already locked
          this.dialogMessage = 'Record locked by another user!';
          this.showDialog = true;
        }
      }
    );
  }

  private addItem(): void {
    this.router.navigate([RoutingPaths.channelDescriptions, 'new']);
  }
}



