import { BaseDataSource } from '../../common/components/base-data-source';
import { Failure } from '../../dao/failure';
import { FailuresService } from '../../services/failures.service';
import { Observable } from 'rxjs/Observable';
import { ListResults } from '../../common/services/crud.service';
import { AdvancedTableColumnFilter } from '../../common/components/advanced-table-data';

export class FailuresDataSource extends BaseDataSource<Failure> {
    constructor(private service: FailuresService) {
      super();
    }

    getData(
      filter: AdvancedTableColumnFilter[] = [],
      sortField: string = '',
      sortDirection: string = 'asc',
      pageIndex: number = 0,
      pageSize: number = 10,
      fields?: string[]
    ): Observable<ListResults<Failure>> {
      return this.service.getItems(
        filter,
        sortField,
        sortDirection,
        pageIndex,
        pageSize,
        fields
      );
    }
  }



