import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { RoutingPaths } from '../../utils/routing-paths';
import { Failure } from '../../dao/failure';
import { FailuresService } from '../../services/failures.service';
import { FieldDefinition } from '../../common/components/dynamic-forms/field-definition';
import { FailureFieldMapper } from '../../dao/failure-field-mapper';
import { CanComponentDeactivate } from '../../guards/can-deactivate.guard';

@Component({
  selector: 'app-failure-detail',
  templateUrl: './failure-detail.component.html',
  styleUrls: ['./failure-detail.component.scss']
})
export class FailureDetailComponent implements OnInit, CanComponentDeactivate {
  private isNew: boolean;
  item: Failure;
  itemDefinition: Array<FieldDefinition> = [
    {
      key: this.fieldMapper.fieldMap['id'],
      label: 'Id',
      type: 'number',
      hidden: true,
      isId: true,
      required: true,
    },
    {
      key: this.fieldMapper.fieldMap['status'],
      label: '',
      type: 'label',
    },
    {
      key: this.fieldMapper.fieldMap['cameTitle'],
      label: 'Full Name',
      type: 'text',
      disabled: true,
    },
    {
      key: this.fieldMapper.fieldMap['name'],
      label: 'Failure Name',
      type: 'text',
      required: true,
    },
    {
      key: this.fieldMapper.fieldMap['faultTitle'],
      label: 'Failure Title',
      type: 'text',
      required: true,
    },
    {
      key: this.fieldMapper.fieldMap['isCarbRelevant'],
      label: 'Relevant for CARB OBD Summary Table',
      type: 'checkbox',
    },
    {
      key: this.fieldMapper.fieldMap['failureGroupId'],
      label: 'Failure Group',
      type: 'number',
    },
    {
      key: this.fieldMapper.fieldMap['componentId'],
      label: 'Component',
      type: 'number',
      required: true,
    },
    {
      key: this.fieldMapper.fieldMap['responsibleId'],
      label: 'Responsible',
      type: 'number',
      required: true,
    },
    {
      key: this.fieldMapper.fieldMap['carbComponentId'],
      label: 'CARB Standardized Component',
      type: 'number',
    },
    {
      key: this.fieldMapper.fieldMap['monitorStrategyDescription'],
      label: 'Monitoring Strategy Description',
      type: 'textarea',
      required: true,
    },
    {
      key: this.fieldMapper.fieldMap['timeRequired'],
      label: 'Time Required',
      type: 'textarea',
      required: true,
    },
    {
      key: this.fieldMapper.fieldMap['comment'],
      label: 'Comment',
      type: 'textarea',
    },
  ];

  errorMessage: string;
  showDialog = false;
  dialogMessage: string;
  protected lockedByCurrentUser = false;

  extraButtons: any[] = [
    {label: 'Fault Description', actionId: 'faultDescription', disabled: false},
    {label: 'Ambient Conditions', actionId: 'ambientConditions', disabled: false},
    {label: 'CARB Description', actionId: 'carbDescription', disabled: false},
    {label: 'CARB FID', actionId: 'carbFid', disabled: false},
  ];
  showFaultDescriptionDialog = false;
  showAmbientConditionsDialog = false;
  showCarbDescriptionDialog = false;
  showCarbFidDialog = false;
  showFnrFunctionsDescriptionDialog = false;

  faultDescriptionTabs: any[] = [
    {label: 'General Remark'},
    {label: 'Test Condition'},
    {label: 'Fault Detection'},
    {label: 'Defect Debouncing'},
    {label: 'Fault Healing'},
    {label: 'Healing Debouncing'},
    {label: 'System Reaction'},
    {label: 'Fault Disabling'},
    {label: 'Fault Cause'},
    {label: 'Fault Repair'},
  ];

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private dataService: FailuresService,
    private fieldMapper: FailureFieldMapper
  ) { }

  ngOnInit() {
    this.isNew = this.route.snapshot.url[1].path === 'new';

    if (this.isNew) {
      this.lockedByCurrentUser = true;
      this.item = {
        id: 0,
        name: null,
        faultTitle: null,
        failureGroupId: null,
        comment: null,
        cameTitle: null,
        componentId: null,
        carbComponentId: null,
        monitorStrategyDescription: null,
        timeRequired: null,
        responsibleId: null,
        isCarbRelevant: null,
      };
    } else {
      const id = this.route.snapshot.params['id'];
      const statusIndex = this.itemDefinition.findIndex(item => item.key === this.fieldMapper.fieldMap['status']);
      this.dataService.lockItem(id).subscribe(
        success => {
          this.lockedByCurrentUser = true;
          this.itemDefinition[statusIndex].label = 'Locked by you';
          this.getItem();
        },
        error => {
          if (error.status === 409) { // already locked
            this.lockedByCurrentUser = false;
            this.itemDefinition[statusIndex].label = 'Locked by another user';
            this.getItem();
          }
        },
      );
    }
  }

  private getItem() {
    this.dataService.getItem(this.route.snapshot.params['id'])
      .subscribe((item: Failure) => {
        this.item = item;
      }
    );
  }

  private cancel() {
    this.router.navigate([RoutingPaths.failures]);
  }

  private save(item: Failure) {
    if (this.isNew) {
      this.create(item);
    } else {
      this.update(item);
    }

    this.cancel();
  }

  private update(item: Failure) {
    this.dataService.editItem(item.id, item)
      .subscribe((x: any) => {
        console.log('failure ' + item.id + ' updated');
      }
    );
  }

  private create(item: Failure) {
    // TODO: (No 'Access-Control-Allow-Origin' header is present on the requested resource)
    this.dataService.addItem(item)
      .subscribe((x: any) => {
        console.log('failure ' + item.id + ' created');
      }
    );
  }

  dialogKeepLock() {
    this.showDialog = false;
    this.lockedByCurrentUser = false;
    this.cancel();
  }

  dialogUnlock() {
    this.showDialog = false;
    this.dataService.unlockItem(this.route.snapshot.params['id']).subscribe(
      success => {
        this.lockedByCurrentUser = false;
        this.cancel();
      },
      error => {
        console.log('error unlocking');
      },
    );
  }

  canDeactivate(): boolean {
    if (!this.lockedByCurrentUser) {
      return true;
    }
    this.dialogMessage = 'You have locked this failure, which prevents other users from editing it! What would you like to do?';
    this.showDialog = true;
  }

  private extraAction(actionId: string) {
    if (actionId === 'faultDescription') {
      this.showFaultDescriptionDialog = true;
    } else if (actionId === 'ambientConditions') {
      this.showAmbientConditionsDialog = true;
    } else if (actionId === 'carbDescription') {
      this.showCarbDescriptionDialog = true;
    } else if (actionId === 'carbFid') {
      this.showCarbFidDialog = true;
    }
  }
}



