import { Component, OnInit, Input, Output, EventEmitter, ViewChild } from '@angular/core';
import { A2lFailureDetail } from '../../../dao/a2l-failure-detail';
import * as Excel from 'exceljs/dist/exceljs.min.js';
import { saveAs } from 'file-saver';

const onOrOffFilterOptions = [
  { label: 'On or Off', value: null },
  { label: 'On', value: 'ON' },
  { label: 'Off', value: 'OFF' },
];

const yesOrNoFilterOptions = [
  { label: 'Yes or No', value: null },
  { label: 'Yes', value: true },
  { label: 'No', value: false },
];

@Component({
  selector: 'app-results',
  templateUrl: './results.component.html',
  styleUrls: ['./results.component.scss']
})
export class ResultsComponent implements OnInit {

  @Input() visibleIn = false;
  @Input() a2lFailureDetails: A2lFailureDetail[];
  @Input() panelTitle: any;
  @Output() visibleOut: EventEmitter<boolean> = new EventEmitter<boolean>();

  @ViewChild('tt') mainTable: any;

  selectedRow: any;
  dblClickedRow: any;
  showDetailsDialog = false;

  // master table
  cols: any[] = [
    {
      header: 'DTC',
      field: 'dtc',
      filterMatchMode: 'contains',
      width: '2rem',
    },
    {
      header: 'Title',
      field: 'title',
      filterMatchMode: 'contains',
      width: '12rem',
    },
    {
      header: 'Name',
      field: 'name',
      filterMatchMode: 'contains',
      width: '8rem',
    },
    {
      header: 'MIL',
      field: 'mil',
      filterMatchMode: 'equals',
      width: '2rem',
      isFilterDropdown: true,
      filterOptions: onOrOffFilterOptions,
    },
    {
      header: 'Dia Lamp',
      field: 'systemLamp',
      filterMatchMode: 'contains',
      width: '4rem',
    },
    {
      header: 'OBD-System',
      field: 'obdSystem',
      filterMatchMode: 'contains',
      width: '4rem',
    },
    {
      header: 'Perf Lim',
      field: 'perfLim',
      filterMatchMode: 'contains',
      width: '6rem',
    },
    {
      header: 'Disabled',
      field: 'disabled',
      filterMatchMode: 'equals',
      width: '2rem',
      isCheckbox: true,
      isFilterDropdown: true,
      filterOptions: yesOrNoFilterOptions,
    },
  ];

  // fidCalibrations (detail table)
  colsSubTable: any[] = [
    {
      header: 'Name',
      field: 'fnrFidCalibrationName',
      width: '20rem',
    },
    {
      header: 'Description',
      field: 'fnrFidCalibrationDescription',
    },
  ];

  constructor() { }

  ngOnInit() {
    this.selectedRow = this.a2lFailureDetails[0];
  }

  close() {
    this.visibleIn = false;
    this.visibleOut.emit(false);
  }

  cancelRowUnselect(event) {
    this.selectedRow = event.data;
    this.mainTable.selection = this.selectedRow;
  }

  onRowDblClick(row) {
    this.dblClickedRow = row;
    this.showDetailsDialog = true;
  }

  createReport() {

    const sheet1Data: any[] = [
      [ 'Project:', this.panelTitle['a2l'] ],
      [ 'Dataset:', this.panelTitle['hex'] ],
      [],
      Object.keys(this.failureDetailToXlsRow(this.a2lFailureDetails[0]))
    ];
    for (const failureDetail of this.a2lFailureDetails) {
      sheet1Data.push(Object.values(this.failureDetailToXlsRow(failureDetail)));
    }

    const sheet2Data: any[] = [
      [], // as in legacy, first row is empty
      [
        'Usage Comment',
        'Parameter Name',
        'Value',
        'Compare Value',
      ],
    ];

    const workbook = new Excel.Workbook();
    const worksheet1 = workbook.addWorksheet('Sheet1', {views: [{showGridLines: false}]});
    const worksheet2 = workbook.addWorksheet('Sheet2', {views: [{showGridLines: false}]});

    const grayFill = {type: 'pattern', pattern: 'solid', fgColor: {argb: 'FFC0C0C0'}};
    const baseStyle = {
      font: {name: 'Arial', size: 9},
      alignment: {vertical: 'middle', wrapText: true},
    };

    worksheet1.columns = [
      { width: 18, style: baseStyle }, // Name
      { width: 40, style: baseStyle }, // Title
      { width: 10, style: baseStyle }, // DTC
      { width: 20, style: baseStyle }, // Component
      { width: 22, style: baseStyle }, // OBDCode
      { width: 25, style: baseStyle }, // OBD System (Readiness)
      { width: 60, style: baseStyle }, // General Remark
      { width: 60, style: baseStyle }, // Test Condition
      { width: 60, style: baseStyle }, // Fault Detection
      { width: 60, style: baseStyle }, // Defect Debouncing
      { width: 60, style: baseStyle }, // Fault Healing
      { width: 60, style: baseStyle }, // Healing Debouncing
      { width: 60, style: baseStyle }, // System Reaction
      { width: 45, style: baseStyle }, // Fid Name
      { width: 60, style: baseStyle }, // Fid Description
      { width: 60, style: baseStyle }, // Fault Disabling
      { width: 10, style: baseStyle }, // Disabled
      { width: 60, style: baseStyle }, // Failure Cause
      { width: 60, style: baseStyle }, // Failure Repair
      { width: 20, style: baseStyle }, // Fault Storage
      { width: 20, style: baseStyle }, // Debouncing Tester Present
      { width: 20, style: baseStyle }, // Reset On Afterrun
      { width: 20, style: baseStyle }, // Reset Behaviour
      { width: 20, style: baseStyle }, // Disable Behaviour
      { width: 20, style: baseStyle }, // Classification
      { width: 10, style: baseStyle }, // Error Class
      { width: 10, style: baseStyle }, // Priority
      { width: 15, style: baseStyle }, // Visible OBD scan tool
      { width: 15, style: baseStyle }, // Long Term Failure
      { width: 15, style: baseStyle }, // Performance Limiter
      { width: 30, style: baseStyle }, // Inducement
      { width: 30, style: baseStyle }, // DIUMPR
      { width: 30, style: baseStyle }, // SVS
      { width: 18, style: baseStyle }, // SVS ON
      { width: 18, style: baseStyle }, // SVS OFF
      { width: 10, style: baseStyle }, // MIL
      { width: 18, style: baseStyle }, // Validation (MIL ON)
      { width: 18, style: baseStyle }, // Healing (MIL OFF)
      { width: 18, style: baseStyle }, // Erasing memory
      { width: 18, style: baseStyle }, // Erasing pending, healed
      { width: 18, style: baseStyle }, // Erasing pending, no tested
      { width: 45, style: baseStyle }, // Amb Cond Name
      { width: 60, style: baseStyle }, // Amb Cond Description
      { width: 18, style: baseStyle }, // DTC SAE (SPN-FMI)
      { width: 18, style: baseStyle }, // DTCM & DTCMLo
      { width: 18, style: baseStyle }, // Blink Code
    ];

    worksheet2.columns = [
      { width: 37, style: baseStyle },
      { width: 37, style: baseStyle },
      { width: 60, style: baseStyle },
      { width: 60, style: baseStyle },
    ];

    worksheet1.addRows(sheet1Data);
    worksheet2.addRows(sheet2Data);

    worksheet1.getRow(1).font = worksheet1.getRow(2).font = {name: 'Arial', size: 11, bold: true};
    worksheet1.getRow(1).alignment = worksheet1.getRow(2).alignment = {wrapText: false};

    const border = {
      top: {style: 'medium'},
      left: {style: 'medium'},
      bottom: {style: 'medium'},
      right: {style: 'medium'}
    };

    // borders and fill header row on sheet1 - headers are on row 4
    const sheet1Row4 = worksheet1.getRow(4);
    sheet1Row4.font = {name: 'Arial', size: 9, bold: true};
    for (let c = 1; c <= worksheet1.columnCount; c++) {
      sheet1Row4.getCell(c).fill = grayFill;
      sheet1Row4.getCell(c).border = border;
    }

    // borders on sheet1 - headers are on row 4, which already have borders, so we start at 5
    for (let r = 5; r <= worksheet1.rowCount; r++) {
      const row = worksheet1.getRow(r);
      for (let c = 1; c <= worksheet1.columnCount; c++) {
        row.getCell(c).border = border;
      }
    }

    // borders and fill header row on sheet2 - headers are on row 2
    // row 1 height = 1px (from legacy tool report)
    worksheet2.getRow(1).height = 1;
    const sheet2Row2 = worksheet2.getRow(2);
    sheet2Row2.font = {name: 'Arial', size: 9, bold: true};
    for (let c = 1; c <= worksheet2.columnCount; c++) {
      sheet2Row2.getCell(c).fill = grayFill;
      sheet2Row2.getCell(c).border = border;
    }

    // create and download file
    workbook.xlsx.writeBuffer().then( data => {
      const blob = new Blob( [data], {type: 'application/octet-stream'} );
      saveAs(
        blob,
        this.panelTitle['a2l'] && this.panelTitle['hex'] ?
        (
          this.panelTitle['a2l'].slice(0, this.panelTitle['a2l'].lastIndexOf('.')) +
          '_FnR_' +
          this.panelTitle['hex'].slice(0, this.panelTitle['hex'].lastIndexOf('.')) + '.xlsx'
        ) :
        'FnR_parsing_report.xlsx'
      );
    });
  }

  private failureDetailToXlsRow(failureDetail: A2lFailureDetail): any {

    const row = {
      'Name': failureDetail.name || '',
      'Title': failureDetail.title || '',
      'DTC': failureDetail.dtc || '',
      'Component': failureDetail.component || '',
      'OBDCode': failureDetail.obdCode || '',
      'OBD System (Readiness)': failureDetail.obdSystem || '',
      'General Remark': failureDetail.generalRemarks || '',
      'Test Condition': failureDetail.testConditon || '',
      'Fault Detection': failureDetail.faultDetection || '',
      'Defect Debouncing': failureDetail.defectDebouncing || '',
      'Fault Healing': failureDetail.faultHealing || '',
      'Healing Debouncing': failureDetail.healingDebouncing || '',
      'System Reaction': failureDetail.systemReaction || '',
      'Fid Name': '', // this will be populated after, using fidCalibrations array
      'Fid Description': '', // this will be populated after, using fidCalibrations array
      'Fault Disabling': failureDetail.faultDisabling || '',
      'Disabled': failureDetail.disabled || '',
      'Failure Cause': failureDetail.failureCause || '',
      'Failure Repair': failureDetail.failureRepair || '',
      'Fault Storage': failureDetail.faultStorage || '',
      'Debouncing Tester Present': failureDetail.debouncingWhileTesterPresent || '',
      'Reset On Afterrun': failureDetail.resetAfterrun || '',
      'Reset Behaviour': failureDetail.resetBehaviour || '',
      'Disable Behaviour': failureDetail.disableBehaviour || '',
      'Classification': failureDetail.classification || '',
      'Error Class': failureDetail.errorClass || '',
      'Priority': failureDetail.priority || '',
      'Visible OBD scan tool': failureDetail.visibleScanTool || '',
      'Long Term Failure': failureDetail.longTermFailure || '',
      'Performance Limiter': failureDetail.perfLim || '',
      'Inducement': failureDetail.inducement || '',
      'DIUMPR': failureDetail.diumpr || '',
      'SVS': failureDetail.systemLamp || '',
      'SVS ON': failureDetail.systemLampActivation || '',
      'SVS OFF': failureDetail.systemLampDeactivation || '',
      'MIL': failureDetail.mil || '',
      'Validation (MIL ON)': failureDetail.validationMil || '',
      'Healing (MIL OFF)': failureDetail.healingMil || '',
      'Erasing Memory': failureDetail.dtcErasingFromMemory || '',
      'Erasing pending, healed': failureDetail.dtcErasingPendingHealed || '',
      'Erasing pending, no tested': failureDetail.dtcErasingPending || '',
      'Amb Cond Name': '', // this will be populated after, using ambientConditions array
      'Amb Cond Description': '', // this will be populated after, using ambientConditions array
      'DTC SAE (SPN-FMI)': failureDetail.dtc || '', // from the sample report this seems to be the same as dtc
      'DTCM & DTCMLo': failureDetail.dtcm || '',
      'Blink Code': failureDetail.blinkCode || '',
    };

    // populate fid columns, using fidCalibrations array
    for (let index = 0; index < failureDetail.fidCalibrations.length; index++) {
      row['Fid Name'] +=
        (index + 1) + ') ' + (failureDetail.fidCalibrations[index].fnrFidCalibrationName || '');
      row['Fid Description'] +=
        (index + 1) + ') ' + (failureDetail.fidCalibrations[index].fnrFidCalibrationDescription || '');

      if (index !== failureDetail.fidCalibrations.length - 1) {
        row['Fid Name'] += '\r\n';
        row['Fid Description'] += '\r\n';
      }
    }

    // populate ambient conditions columns, using ambientConditions array
    for (let index = 0; index < failureDetail.ambientConditions.length; index++) {
      row['Amb Cond Name'] +=
        (index + 1) + ') ' + (failureDetail.ambientConditions[index].fnrAmbientConditionName || '');
      row['Amb Cond Description'] +=
        (index + 1) + ') ' + (failureDetail.ambientConditions[index].fnrAmbientConditionDescription || '');

      if (index !== failureDetail.ambientConditions.length - 1) {
        row['Amb Cond Name'] += '\r\n';
        row['Amb Cond Description'] += '\r\n';
      }
    }

    return row;
  }
}



