import { BaseDataSource } from '../../common/components/base-data-source';
import { UsersService } from '../../services/users.service';
import { User } from '../../dao/user';
import { Observable } from 'rxjs/Observable';
import { ListResults } from '../../common/services/crud.service';
import { AdvancedTableColumnFilter } from '../../common/components/advanced-table-data';

export class UsersDataSource extends BaseDataSource<User> {
    constructor(private service: UsersService) {
      super();
    }

    getData(
      filter: AdvancedTableColumnFilter[] = [],
      sortField: string = '',
      sortDirection: string = 'asc',
      pageIndex: number = 0,
      pageSize: number = 10
    ): Observable<ListResults<User>> {
      return this.service.getItems(
        filter,
        sortField,
        sortDirection,
        pageIndex,
        pageSize
      );
    }
  }



