import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';

import { Failure } from '../dao/failure';
import { ApisService } from '../common/services/apis.service';
import { FailureFieldMapper } from '../dao/failure-field-mapper';
import { LockableCrudService } from '../common/services/lockable-crud.service';

@Injectable()
export class FailuresService extends LockableCrudService<Failure> {
  constructor(apisService: ApisService, fieldMapper: FailureFieldMapper) {
    super(apisService, fieldMapper);
  }

  get apiRoot(): string {
    return 'Failures';
  }

  get lockResourceType(): string {
    return 'failure';
  }

  copyFailures(failures: Failure[], comment: string = ''): Observable<object> {
    const failureIdsToCopy: number[] = failures.map(failure => failure.id);
    return this.apisService.performPut(
      this.baseApiUrlWithRoot + '/copy',
      {
        failures: failureIdsToCopy,
        comment: comment,
      },
      true
    );
  }

  updateFailuresProjects(failures: Failure[]): Observable<object> {
    return this.apisService.performPut(
      this.baseApiUrlWithRoot,
      { failures: failures },
      true
    );
  }

  getFaultDescriptions(failureId: number): Observable<object> {
    return this.getSubUrl(failureId, 'faults');
  }

  updateFaultDescriptions(failureId: number, faultDescriptions: object): Observable<object> {
    return this.apisService.performPut(
      this.baseApiUrlWithRoot + '/' + failureId + '/faults',
      faultDescriptions,
      true
    );
  }

  getAmbientConditions(failureId: number): Observable<object> {
    return this.getSubUrl(failureId, 'ambients');
  }

  // TODO: create a service for ambient conditions only
  updateAmbientCondition(ambientCondition: object): Observable<object> {
    return this.apisService.performPut(
      this.apisService.baseApiUrl + '/AmbientConditions/' + ambientCondition['id'],
      // this.baseApiUrlWithRoot + "/" + failureId + "/faults",
      ambientCondition,
      true
    );
  }

  getCarbDescriptions(failureId: number): Observable<object> {
    return this.getSubUrl(failureId, 'carbs');
  }

  updateCarbDescriptions(failureId: number, carbDescriptions: object): Observable<object> {
    return this.apisService.performPut(
      this.baseApiUrlWithRoot + '/' + failureId + '/carbs',
      carbDescriptions,
      true
    );
  }

  // getCarbFids(failureId: number): Observable<object> {
  getCarbFids(failureId: number): Observable<any> {
    return this.getSubUrl(failureId, 'fids');
  }

  updateCarbFids(failureId: number, carbFids: object): Observable<object> {
    // TODO
    console.log('TODO: save CARB FIDs (missing endpoint in api!)');
    console.log(carbFids);
    return Observable.of({});

    // return this.apisService.performPut(
    //   this.baseApiUrlWithRoot + '/' + failureId + '/fids',
    //   carbFids,
    //   true
    // );
  }

  private getSubUrl(failureId: number, subUrl: string): Observable<object> {
    return this.apisService.performGet(
      this.baseApiUrlWithRoot + '/' + failureId + '/' + subUrl,
      true
    );
  }
}



