import { Component, Input, Output, EventEmitter, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { fnrFunctionsDescription } from './fnr-functions-description';
import { FailuresService } from '../../../services/failures.service';
import { FailureFieldMapper } from '../../../dao/failure-field-mapper';
import { LabelDescriptionsService } from '../../../services/label-descriptions.service';
import { ChannelDescription } from '../../../dao/channel-description';
import { ChannelDescriptionsService } from '../../../services/channel-descriptions.service';
import { ChannelDescriptionFieldMapper } from '../../../dao/channel-description-field-mapper';

@Component({
  selector: 'app-fault-description',
  templateUrl: './fault-description.component.html',
  styleUrls: ['./fault-description.component.scss']
})
export class FaultDescriptionComponent implements OnInit {
  @Input() visibleIn = false;
  @Output() visibleOut: EventEmitter<boolean> = new EventEmitter<boolean>();

  failureId: number;

  showChangeLabelDialog = false;
  changeLabelDialogParams = {
    tabIdx: null,
    levelIdx: null,
    cardIdx: null,
    currentDecisionLabel: '',
    newDecisionLabel: '',
  };

  showAttentionDialog = false;
  attentionMessage = '';

  comparissonOperators = [
    { label: '=', value: '=' },
    { label: '<', value: '<' },
    { label: '>', value: '>' },
    { label: '<=', value: '<=' },
    { label: '>=', value: '>=' },
    { label: '<>', value: '<>' },
    { label: 'Bit', value: 'Bit' },
  ];

  failureWithFaults: any;
  faultDescriptionTabs: any[];
  cardPositionByTabAndLabel: any[];

  showBitSyntaxDialog = false;
  showFnrFunctionsDescriptionDialog = false;
  functionsDescriptionRows = fnrFunctionsDescription;
  showCloseConfirmDialog = false;

  labelDescriptions: string[];
  filteredLabelDescriptions: string[];
  labelDescriptionsSearchDisabled = true;

  fidConditions: any[] = [];
  selectedFidCondition: any;
  fidReleaseConditions: any[] = [];
  currentFidReleaseCondition: any = null;

  showFidConditionDialog = false;
  manualManipulation = false;
  fidConditionText = '';

  rules = [
    // { label: '', value: null },
    { label: '=', value: '=' },
    { label: '<', value: '<' },
    { label: '>', value: '>' },
    { label: '<=', value: '<=' },
    { label: '>=', value: '>=' },
    { label: '<>', value: '<>' },
    { label: 'Bit', value: 'Bit' },
    { label: 'Bit!', value: 'Bit!' },
  ];

  links = [
    { label: '', value: null },
    { label: 'AND', value: 'AND' },
    { label: 'OR', value: 'OR' },
  ];

  fidConditionRows: any[] = [
    {
      label: null,
      rule: null,
      value: null,
      link: null,
    }
  ];

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private dataService: FailuresService,
    private labeDescriptionsDataService: LabelDescriptionsService,
    private fidConditionsDataService: ChannelDescriptionsService,
    private fidConditionFieldMapper: ChannelDescriptionFieldMapper,
    private fieldMapper: FailureFieldMapper
  ) { }

  ngOnInit(): void {
    this.resetFaultDescriptionTabs();
    this.failureId = this.route.snapshot.params['id'];
    this.getAllFaultDescriptions(this.failureId);
    this.getAllLabelDescriptions();
    this.getAllFidConditions();
  }

  private resetFaultDescriptionTabs() {
    this.faultDescriptionTabs = [
      { id: 'generalRemark', label: 'General Remark', cards: [] },
      { id: 'testCondition', label: 'Test Condition', cards: [] },
      { id: 'faultDetection', label: 'Fault Detection', cards: [] },
      { id: 'defectDebouncin', label: 'Defect Debouncing', cards: [] },
      { id: 'faultHealing', label: 'Fault Healing', cards: [] },
      { id: 'healingDebouncing', label: 'Healing Debouncing', cards: [] },
      { id: 'systemReaction', label: 'System Reaction', cards: [] },
      { id: 'faultDisabling', label: 'Fault Disabling', cards: [] },
      { id: 'faultCause', label: 'Fault Cause', cards: [] },
      { id: 'faultRepair', label: 'Fault Repair', cards: [] },
    ];

    this.cardPositionByTabAndLabel = [];
    for (let idx = 0; idx < this.faultDescriptionTabs.length; idx++) {
      this.cardPositionByTabAndLabel[idx] = [];
    }
  }

  private close() {
    this.visibleIn = false;
    this.showCloseConfirmDialog = false;
    this.showFnrFunctionsDescriptionDialog = false;
    this.visibleOut.emit(false);
  }

  private saveFaultDescriptions() {
    const faultDescriptionsRequest = { id: this.failureId };

    for (const tab of this.faultDescriptionTabs) {
      const tabName = tab.id + 's';
      faultDescriptionsRequest[tabName] = [];
      for (const cardLevel of tab.cards) {
        for (const card of cardLevel) {
          for (const condition of card.conditions) {
            faultDescriptionsRequest[tabName].push({
              id: condition.id || 0,
              failureId: this.failureId,
              inputLabel: card.inputLabel,
              decisionOperator: condition.operator || '',
              decisionValue: condition.value || '',
              decisionText: condition.text || '',
              outputLabel: condition.outputLabel || '',
              comment: null,
            });
          }
        }
      }
    }

    this.dataService.updateFaultDescriptions(this.failureId, faultDescriptionsRequest).subscribe(
      result => { this.close(); },
      error => { alert('An error has occurred!'); console.log(error); }
    );
  }

  private getAllFaultDescriptions(failureId: number) {
    this.dataService.getFaultDescriptions(failureId).subscribe(
      result => {
        this.failureWithFaults = result;
        this.faultDescriptionTabs[0].cards = this.groupCardsByLevel(this.groupConditionsByCard(result['generalRemarks'], 0));
        this.faultDescriptionTabs[1].cards = this.groupCardsByLevel(this.groupConditionsByCard(result['testConditions'], 1));
        this.faultDescriptionTabs[2].cards = this.groupCardsByLevel(this.groupConditionsByCard(result['faultDetections'], 2));
        this.faultDescriptionTabs[3].cards = this.groupCardsByLevel(this.groupConditionsByCard(result['defectsDebouncing'], 3));
        this.faultDescriptionTabs[4].cards = this.groupCardsByLevel(this.groupConditionsByCard(result['faultHealings'], 4));
        this.faultDescriptionTabs[5].cards = this.groupCardsByLevel(this.groupConditionsByCard(result['healingDebouncings'], 5));
        this.faultDescriptionTabs[6].cards = this.groupCardsByLevel(this.groupConditionsByCard(result['systemReactions'], 6));
        this.faultDescriptionTabs[7].cards = this.groupCardsByLevel(this.groupConditionsByCard(result['faultDisablings'], 7));
        this.faultDescriptionTabs[8].cards = this.groupCardsByLevel(this.groupConditionsByCard(result['faultCauses'], 8));
        this.faultDescriptionTabs[9].cards = this.groupCardsByLevel(this.groupConditionsByCard(result['faultRepairs'], 9));
      }
    );
  }

  private getAllLabelDescriptions() {
    this.labeDescriptionsDataService
      .getItems([], 'name', 'asc', null, null)
      .subscribe(result => {
        this.labelDescriptions = result.data.map(label => label.name);
        this.filteredLabelDescriptions = this.labelDescriptions;
        this.labelDescriptionsSearchDisabled = false;
      });
  }

  private getAllFidConditions() {
    this.fidConditions = [];
    this.fidConditionsDataService
      .getItems(
        [{field: this.fidConditionFieldMapper.fieldMap.name, value: 'FId_', matchMode: 'startsWith'}],
        this.fidConditionFieldMapper.fieldMap.name,
        'asc',
        null,
        null
      )
      .subscribe(result => {
        // this.fidConditions = result.data;
        result.data.map((fidCondition: ChannelDescription) => {
          this.fidConditions.push({value: fidCondition.id, label: fidCondition.name});
        });
      })
    ;
  }

  private groupConditionsByCard(conditions: any[], tabIdx: number): ConditionCard[] {
    const cards: ConditionCard[] = [];
    const totalConditions = !conditions ? 0 : conditions.length;
    const tabCardPositionByLabel = this.cardPositionByTabAndLabel[tabIdx];
    tabCardPositionByLabel['Root'] = {level: 0, order: 0};

    if (totalConditions === 0) { // if no conditions => create an empty root card
      cards.push(
        {
          level: 0,
          order: 0,
          inputLabel: 'Root',
          conditions: [{}],
        }
      );
      return cards;
    }

    for (let i = 0; i < totalConditions; i++) {
      const condition = conditions[i];

      const conditionForCard: Condition = {
        id: condition.id,
        text: condition.decisionText,
        operator: condition.decisionOperator,
        value: condition.decisionValue,
        outputLabel: condition.outputLabel,
      };

      const idx = cards.findIndex((card: ConditionCard) => card.inputLabel === condition.inputLabel);

      if (idx > -1) { // card already in array => append condition to it
        cards[idx].conditions.push(conditionForCard);

        if (conditionForCard.outputLabel && conditionForCard.outputLabel !== '') {
          tabCardPositionByLabel[conditionForCard.outputLabel] =
            tabCardPositionByLabel[conditionForCard.outputLabel] ||
            { level: cards[idx].level + 1, order: cards[idx].conditions.length - 1 };
        }
      } else { // add a card for this condition
        const level: number = tabCardPositionByLabel[condition.inputLabel] ? tabCardPositionByLabel[condition.inputLabel].level : 1;
        const order: number = tabCardPositionByLabel[condition.inputLabel] ? tabCardPositionByLabel[condition.inputLabel].order : 0;

        cards.push(
          {
            level: level,
            order: order,
            inputLabel: condition.inputLabel,
            conditions: [conditionForCard],
          }
        );

        if (conditionForCard.outputLabel && conditionForCard.outputLabel !== '') {
          tabCardPositionByLabel[conditionForCard.outputLabel] =
            tabCardPositionByLabel[conditionForCard.outputLabel] || {level: level + 1, order: 0};
        }
      }
    }

    return cards;
  }

  private groupCardsByLevel(cards: ConditionCard[]): ConditionCard[][] {
    const levels: ConditionCard[][] = [];

    for (const card of cards) {
      levels[card.level] = levels[card.level] || [];
      levels[card.level].push(card);
    }

    return levels;
  }

  private filterLabelDescriptions(str: string) {
    if (!str || str === '') {
      this.filteredLabelDescriptions = this.labelDescriptions;
    } else {
      this.filteredLabelDescriptions = this.labelDescriptions.filter(name =>
        name.toUpperCase().startsWith(str.toUpperCase())
      );
    }
  }

  addSelectedLabelDescriptionBlock(tabIdx: number, levelIdx: number, condition: Condition) {
    const tabCards = this.faultDescriptionTabs[tabIdx].cards;
    const currentCard = this.getCardByCondition(tabIdx, condition);
    const idxCurrentCard = tabCards[currentCard.level].findIndex(card => card.inputLabel === currentCard.inputLabel);

    const idxCurrentCardCondition =
      tabCards[currentCard.level][idxCurrentCard].conditions.findIndex(
        cardCondition => cardCondition.outputLabel === condition.outputLabel
      )
    ;
    let conditionsBeforeCurrent = 0;

    for (let levelCardsIdx = 0; levelCardsIdx < idxCurrentCard; levelCardsIdx++) {
      conditionsBeforeCurrent += tabCards[currentCard.level][levelCardsIdx].conditions.length;
    }
    const cardOrder = conditionsBeforeCurrent + idxCurrentCardCondition;

    // check if label exists
    const existingCard = this.getCardByTabAndInputLabel(tabIdx, condition.outputLabel);

    if (existingCard) { // if exists, don't create a new node

      // check if selected label is the same node or a parent node
      // if so, don't create the relation, send an error message instead (don't allow loops)
      if (currentCard.inputLabel === condition.outputLabel || this.isCardParent(tabIdx, currentCard, existingCard)) {
        this.attentionMessage = 'A loop is attempted for node "'
          + currentCard.inputLabel + '" where one of its branches references a parent node! Input can\'t be set.';
        this.showAttentionDialog = true;
        condition.outputLabel = '';
        return;
      }

      // check if label is a sibling,
      // if so, check if the referenced node is before or after the referencing condition branch
      if (existingCard.level === currentCard.level) {
        const idxExistingCard = tabCards[currentCard.level].findIndex(card => card.inputLabel === existingCard.inputLabel);

        // if it is after, create the relation and move the referenced node to the next level
        if (idxCurrentCard < idxExistingCard) {
          tabCards[levelIdx] = tabCards[levelIdx] || [];
          existingCard.level++;
          existingCard.order = cardOrder;
          this.insertCard(tabIdx, existingCard);
        }
        // else { referenced node is before => don't do anything (leave the reference as is)! }
      }
      // else { it's a node on a higher level! => don't do anything (leave the reference as is)! }
    } else { // create a new empty card
      const card = new ConditionCard();
      card.level = levelIdx;
      card.order = cardOrder;
      card.inputLabel = condition.outputLabel;
      card.conditions = [{}];
      this.insertCard(tabIdx, card);
    }
  }

  private insertCard(tabIdx: number, card: ConditionCard) {
    this.faultDescriptionTabs[tabIdx].cards[card.level] = this.faultDescriptionTabs[tabIdx].cards[card.level] || [];
    let levelCards = this.faultDescriptionTabs[tabIdx].cards[card.level];

    for (const levelCard of levelCards) {
      if (levelCard.order >= card.order) {
        levelCard.order++;
      }
    }

    levelCards.push(card);
    levelCards = this.reorderCards(levelCards);
  }

  private reorderCards(cards: ConditionCard[]): ConditionCard[] {
    cards.sort((a, b) => {
      return (a.order > b.order) ? 1 : ((a.order < b.order) ? -1 : 0);
    });
    return cards;
  }

  private isCardParent(tabIdx: number, card: ConditionCard, cardToTest: ConditionCard): boolean {
    const parentCards = this.getCardsByTabAndOutputLabel(tabIdx, card.inputLabel);

    for (const parentCard of parentCards) {
      if (parentCard.inputLabel === cardToTest.inputLabel) {
        return true;
      } else {
        return this.isCardParent(tabIdx, parentCard, cardToTest);
      }
    }

    return false;
  }

  private getCardByTabAndInputLabel(tabIdx: number, label: string): ConditionCard {
    for (const card of this.faultDescriptionTabs[tabIdx].cards) {
      const idx = card.findIndex(c => c.inputLabel === label);
      if (idx > -1) {
        return card[idx];
      }
    }
    return null;
  }

  private getCardsByTabAndOutputLabel(tabIdx: number, label: string): ConditionCard[] {
    const cards: ConditionCard[] = [];
    for (const card of this.faultDescriptionTabs[tabIdx].cards) {
      const idx = card.findIndex(c => {
        return c.conditions.findIndex(condition => condition.outputLabel === label) > -1;
      });
      if (idx > -1) {
        cards.push(card[idx]);
      }
    }
    return cards;
  }

  private getCardByCondition(tabIdx: number, condition: Condition): ConditionCard {
    for (const card of this.faultDescriptionTabs[tabIdx].cards) {
      const idx = card.findIndex(c => {
        return c.conditions.indexOf(condition) > -1;
      });
      if (idx > -1) {
        return card[idx];
      }
    }
    return null;
  }

  openChangeDecisionLabelDialog(tabIdx: number, levelIdx: number, cardIdx: number) {
    const tabCards = this.faultDescriptionTabs[tabIdx].cards;
    const card = tabCards[levelIdx][cardIdx];
    this.changeLabelDialogParams = {
      tabIdx: tabIdx,
      levelIdx: levelIdx,
      cardIdx: cardIdx,
      currentDecisionLabel: card.inputLabel,
      newDecisionLabel: '',
    };
    this.showChangeLabelDialog = true;
  }

  closeChangeDecisionLabelDialog() {
    this.showChangeLabelDialog = false;
    this.changeLabelDialogParams = {
      tabIdx: null,
      levelIdx: null,
      cardIdx: null,
      currentDecisionLabel: '',
      newDecisionLabel: '',
    };
  }

  changeDecisionLabel(tabIdx: number, levelIdx: number, cardIdx: number) {
    const tabCards = this.faultDescriptionTabs[tabIdx].cards;
    const card = tabCards[levelIdx][cardIdx];

    if (this.changeLabelDialogParams.currentDecisionLabel === this.changeLabelDialogParams.newDecisionLabel) {
      this.attentionMessage =
        'Selected label ("' + this.changeLabelDialogParams.newDecisionLabel + '") must be different from the current one.';
      this.showAttentionDialog = true;
      return;
    }

    if (this.getCardByTabAndInputLabel(tabIdx, this.changeLabelDialogParams.newDecisionLabel)) {
      this.attentionMessage =
        'A node with the selected label ("' + this.changeLabelDialogParams.newDecisionLabel + '") has already been added.';
      this.showAttentionDialog = true;
      return;
    }

    // change outputLabel on all conditions of the parent cards (there might be more than one!)
    for (const levelCards of tabCards) {
      for (const c of levelCards) {
        for (const condition of c.conditions) {
          if (condition.outputLabel === this.changeLabelDialogParams.currentDecisionLabel) {
            condition.outputLabel = this.changeLabelDialogParams.newDecisionLabel;
          }
        }
      }
    }

    // change inputLabel on the current card
    card.inputLabel = this.changeLabelDialogParams.newDecisionLabel;

    this.showChangeLabelDialog = false;
    this.changeLabelDialogParams = {
      tabIdx: null,
      levelIdx: null,
      cardIdx: null,
      currentDecisionLabel: '',
      newDecisionLabel: '',
    };
  }

  addConditionBlock(tabIdx: number, levelIdx: number, cardIdx: number) {
    this.faultDescriptionTabs[tabIdx].cards[levelIdx][cardIdx].conditions.push({});
  }

  removeConditionBlock(tabIdx: number, levelIdx: number, cardIdx: number, conditionIdx: number) {
    const tabCards = this.faultDescriptionTabs[tabIdx].cards;
    const levelCards = tabCards[levelIdx];
    const conditionOutputLabel = levelCards[cardIdx].conditions[conditionIdx].outputLabel;
    let referencingCards: ConditionCard[] = [];

    // check if the condition is the last reference for another card
    if (conditionOutputLabel && conditionOutputLabel !== '') {
      const referencedCard = this.getCardByTabAndInputLabel(tabIdx, conditionOutputLabel);
      if (referencedCard) {
        referencingCards = this.getCardsByTabAndOutputLabel(tabIdx, referencedCard.inputLabel);
        if (referencingCards && referencingCards.length === 1) {
          this.attentionMessage = 'Deleting this branch will remove the last reference to node "'
            + referencingCards[0].inputLabel + '"! Delete the referenced node first.';
          this.showAttentionDialog = true;
          return; // don't delete it
        }
      }
    }

    // if the condition to remove is the last one in the card
    if (levelCards[cardIdx].conditions.length === 1) {
      const cardInputLabel = levelCards[cardIdx].inputLabel;

      // clear card references
      referencingCards = this.getCardsByTabAndOutputLabel(tabIdx, cardInputLabel);
      for (const card of referencingCards) {
        for (const condition of card.conditions) {
          if (condition.outputLabel === cardInputLabel) {
            condition.outputLabel = '';
          }
        }
      }

      // if the card is the last one in the level => remove the level
      if (levelCards.length === 1) {
        tabCards.splice(levelIdx, 1);
      } else { // remove the card
        levelCards.splice(cardIdx, 1);
      }
    } else { // remove the condition
      levelCards[cardIdx].conditions.splice(conditionIdx, 1);
    }
  }

  addSelectedFidCondition() {
    if (this.fidReleaseConditions.findIndex(fidReleaseCondition => fidReleaseCondition.id === this.selectedFidCondition) < 0) {
      const fidConditionIdx = this.fidConditions.findIndex(fidCondition => fidCondition.value === this.selectedFidCondition);
      this.fidReleaseConditions.push(
        {
          id: this.fidConditions[fidConditionIdx].value,
          name: this.fidConditions[fidConditionIdx].label,
          inverted: false,
          condition: '',
        }
      );
      this.selectedFidCondition = null;
      // remove the selected FID from the dropdown options
      this.fidConditions.splice(fidConditionIdx, 1);
    } else {
      alert('FID already added!');
    }
  }

  removeFidCondition(fidCondition) {
    // remove FID from table...
    let idx = this.fidReleaseConditions.findIndex(fid => fid.id = fidCondition.id);
    this.fidReleaseConditions.splice(idx, 1);

    // add it back to the dropdown options (ordered by name)
    idx = this.fidConditions.findIndex(fid => fid.label.toLowerCase() > fidCondition.name.toLowerCase());
    if (idx >= 0) {
      this.fidConditions.splice(idx, 0, {value: fidCondition.id, label: fidCondition.name});
    } else { // if it's the last of the array => use push()
      this.fidConditions.push({value: fidCondition.id, label: fidCondition.name});
    }
  }

  editFidCondition(fidCondition) {
    if (fidCondition.condition) {
      this.fidConditionRows = this.parseFidConditionText(fidCondition.condition);
    }
    this.showFidConditionDialog = true;
    this.currentFidReleaseCondition = fidCondition;
  }

  onFidReleaseRowDblClick(fidCondition) {
    // fidCondition.condition += "dblClick_";
    this.editFidCondition(fidCondition);
  }

  updateFidConditionText(): boolean {
    let condition = '';
    const numRows = this.fidConditionRows.length;
    let emptyCells = false;

    for (let i = 0; i < numRows; i++) {
      const row = this.fidConditionRows[i];
      if (!row.label || !row.rule || !row.value) {
        emptyCells = true;
        break;
      } else {
        condition += '( ' + row.label + ' ' + row.rule + ' ' + row.value + ' )';
        // only add link if row is not the last
        if (i < numRows - 1) {
          if (!row.link) {
            emptyCells = true;
            break;
          }
          condition += ' ' + row.link + ' ';
        }
      }
    }

    if (emptyCells) {
      alert('There are some empty cells! Please check.');
    } else {
      this.fidConditionText = condition;
    }
    return !emptyCells;
  }

  parseFidConditionText(condition: string): any[] {
    const rows = condition.split('(');
    const numRows = rows.length;
    const conditions = [];

    // i = 1 because the first item is empty!
    for (let i = 1; i < numRows; i++) {
      const rowArr = rows[i].trim().replace(' )', '').split(' ');

      conditions.push(
        {
          label: rowArr[0],
          rule: rowArr[1],
          value: rowArr[2],
          link: rowArr[3] || '', // last row doesn't have link
        }
      );
    }

    return conditions;
  }

  saveFidCondition() {
    if (this.updateFidConditionText()) {
      this.currentFidReleaseCondition.condition = this.fidConditionText;
      this.showFidConditionDialog = false;
      this.fidConditionRows = [];
    }
  }
}

class ConditionCard {
  inputLabel: string;
  conditions: Condition[];
  level: number; // the "tree" level on which the card is in (start at 0)
  order?: number; // the cards order on the level (start at 0)
}

class Condition {
  id?: number;
  text?: string;
  operator?: string;
  value?: string;
  outputLabel?: string;
}



