import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter
} from '@angular/core';

import { AdvancedTableData, AdvancedTableColumnFilter } from '../advanced-table-data';
import { ItemModel } from '../../../dao/item-model';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'advanced-table',
  templateUrl: './advanced-table.component.html',
  styleUrls: ['./advanced-table.component.scss']
})
export class AdvancedTableComponent implements OnInit {
  @Input() listTitle: string;
  @Input() advancedTableData: AdvancedTableData<any>;
  @Input() hasSearchBox = false;
  @Input() hasAddButton = false;
  @Input() hasMultipleSelection = false;
  @Input() hasMultipleDelete = false;
  @Output() addItem = new EventEmitter();
  items: ItemModel[];
  cols: any[] = [];
  actions: any[] = [];
  totalRecords: number;
  paginator = true;
  pageSize = 10;
  rowsPerPageOptions: number[] = [10, 20, 30, 50, 100];
  loading = true;
  lazy = true; // when lazy loading is true, column filtering and sorting must be done when getting the data
  resizableColumns = true;
  reorderableColumns = false;
  selectedItems: ItemModel[];

  constructor() {}

  ngOnInit() {
    if (this.hasMultipleDelete) {
      this.hasMultipleSelection = true;
    }

    for (const column of this.advancedTableData.columns) {
      this.cols.push({
        field: column.matColumnDefValue,
        header: column.description,
        filterMatchMode: column.filterMatchMode,
        filterType: column.filterType,
        filterOptions: column.filterOptions
      });
    }

    if (this.advancedTableData.actions) {
      for (const action of this.advancedTableData.actions) {
        this.actions.push({
          icon: action.icon,
          disabled: action.disabled,
          event: action.event
        });
      }
    }

    if (!this.lazy) {
      this.getItems(null);
    }
  }

  lazyLoadData(event) {
    this.getItems(event);
  }

  getItems(event): void {
    this.loading = true;
    const filters = [];
    let sortField = '';
    let sortOrder = '';
    let pageIndex = 0;

    if (event) {
      this.pageSize = event.rows;

      Object.keys(event.filters).forEach(field => {
        filters.push(new AdvancedTableColumnFilter(field, event.filters[field].value, event.filters[field].matchMode));
      });

      // tslint:disable-next-line:max-line-length
      sortField = event.sortField; // TODO: sorting doesn't work right now 'cause the api doesn't recognize the actual field name! ex. the field from projects "ecu" must be sorted using "Ecu"
      sortOrder = event.sortOrder === 1 ? 'asc' : 'desc';
      pageIndex = event.first / this.pageSize;
    }

    this.advancedTableData.dataSource.getData(filters, sortField, sortOrder, pageIndex, this.pageSize)
    .subscribe(result => {
      this.items = result.data;
      this.totalRecords = result.length;
      this.loading = false;
    });
  }

  add() {
    this.addItem.emit();
  }

  deleteMultiple() {
    console.log('TODO: delete multiple items');
    console.log(this.selectedItems);
  }
}



