import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Observable } from 'rxjs/Observable';
import { catchError, finalize } from 'rxjs/operators';
import { ListResults } from '../services/crud.service';
import { AdvancedTableColumnFilter } from './advanced-table-data';

export abstract class BaseDataSource<T> {
  private dataSubject = new BehaviorSubject<T[]>([]);
  private loadingSubject = new BehaviorSubject<boolean>(false);
  public loading$ = this.loadingSubject.asObservable();

  rowsCount: number;

  // connect(collectionViewer: CollectionViewer): Observable<T[]> {
  //   return this.dataSubject.asObservable();
  // }

  // disconnect(collectionViewer: CollectionViewer): void {
  //   this.dataSubject.complete();
  //   this.loadingSubject.complete();
  // }

  loadData(
    filter: AdvancedTableColumnFilter[] = [],
    sortField: string = '',
    sortDirection: string = 'asc',
    pageIndex: number = 0,
    pageSize: number = 10
  ): void {
    this.loadingSubject.next(true);
    this.getData(filter, sortField, sortDirection, pageIndex, pageSize)
      .pipe(
        catchError((err, caught) => Observable.of([])),
        finalize(() => this.loadingSubject.next(false))
      )
      .subscribe((results: ListResults<T>) => {
        this.rowsCount = results.length;
        this.dataSubject.next(results.data);
      });
  }

  abstract getData(
    filter: AdvancedTableColumnFilter[],
    sortField: string,
    sortDirection: string,
    pageIndex: number,
    pageSize: number
  ): Observable<ListResults<T>>;
}



