import { Injectable } from '@angular/core';
import { environment } from '../../../environments/environment';
import {
  LocalStorageService,
  LocalStorage,
} from 'angular-web-storage';
import { Observable } from 'rxjs/Observable';
import { JwtHelper } from 'angular2-jwt';
// tslint:disable-next-line:import-blacklist
import 'rxjs/Rx';
import { Router } from '@angular/router';
import { HttpClient, HttpHeaders, HttpParams, HttpEvent, HttpRequest } from '@angular/common/http';

@Injectable()
export class ApisService {
  @LocalStorage() private token: string = null;
  @LocalStorage() private user: AccountResponse = null;
  private jwtHelper: JwtHelper = new JwtHelper();

  constructor(
    protected http: HttpClient,
    public storage: LocalStorageService,
    private router: Router
  ) {}

  get baseApiUrl(): string {
    return environment.apisBaseUrl;
  }

  get isLoggedIn(): boolean {
    return this.token != null && !this.jwtHelper.isTokenExpired(this.token);
  }

  private createOptions(
    headers: HttpHeaders,
    needsAuthentication: boolean,
    contentType: string,
    responseType: string = 'arraybuffer',
    reportProgress: boolean = false,
  ): ICustomRequestOptions {
    if (!headers) {
      headers = new HttpHeaders();
    }

    if (contentType) {
      headers = headers.set('Content-Type', contentType);
    }

    if (needsAuthentication) {
      if (!this.token) {
        throw new TokenNotPresentException();
      }

      headers = headers.set('Authorization', 'bearer ' + this.token);
    }

    let requestOptions: ICustomRequestOptions;
    if (responseType === 'text') {
      requestOptions = new CustomRequestOptionsText();
      requestOptions.responseType = 'text';
    } else {
      requestOptions = new CustomRequestOptions();
    }
    requestOptions.headers = headers;
    requestOptions.reportProgress = reportProgress;
    return requestOptions;
  }

  login(username: string, password: string): Observable<LoginResponse> {
    return this.performPost(
      this.baseApiUrl + '/Account/token',
      `username=${username}&password=${password}`,
      false,
      'application/x-www-form-urlencoded'
    ).map((result: LoginResponse) => {
      this.token = result.token;
      return result;
    });
  }

  getUserInfo(): Observable<AccountResponse> {
    if (this.user != null) {
      return Observable.from([this.user]);
    }

    return this.performGet(
      this.baseApiUrl + '/Account',
      true
    ).map((result: AccountResponse) => {
      this.user = result;
      return result;
    });
  }

  logout(): Observable<object> {
    return this.performPost(this.baseApiUrl + '/Account/logout', '', true).map(
      result => {
        this.token = null;
        this.user = null;
        return result;
      }
    );
  }

  performOperation(
    needsAuthentication: boolean,
    method: () => Observable<ArrayBuffer>
  ): Observable<ArrayBuffer> {
    if (needsAuthentication) {
      if (!this.token) {
        throw new TokenNotPresentException();
      }

      if (this.jwtHelper.isTokenExpired(this.token)) {
        this.router.navigate(['login']);
      }
    }

    return method();
  }

  performOperationText(
    needsAuthentication: boolean,
    method: () => Observable<string>
  ): Observable<string> {
    if (needsAuthentication) {
      if (!this.token) {
        throw new TokenNotPresentException();
      }

      if (this.jwtHelper.isTokenExpired(this.token)) {
        this.router.navigate(['login']);
      }
    }

    return method();
  }

  performOperationObject(
    needsAuthentication: boolean,
    method: () => Observable<object>
  ): Observable<object> {
    if (needsAuthentication) {
      if (!this.token) {
        throw new TokenNotPresentException();
      }

      if (this.jwtHelper.isTokenExpired(this.token)) {
        this.router.navigate(['login']);
      }
    }

    return method();
  }

  performGet(
    url: string,
    needsAuthentication: boolean,
    contentType: string = 'application/json',
    headers?: HttpHeaders
  ): Observable<object> {
    const options: CustomRequestOptions = this.createOptions(
      headers,
      needsAuthentication,
      contentType
    );
    return this.performOperation(needsAuthentication, () =>
      this.http.get(url, options)
    );
  }

  performGetText(
    url: string,
    needsAuthentication: boolean,
    contentType: string = 'text',
    headers?: HttpHeaders
  ): Observable<string> {
    const options: CustomRequestOptionsText = this.createOptions(
      headers,
      needsAuthentication,
      contentType,
      'text',
    );

    return this.performOperationText(needsAuthentication, () =>
      this.http.get(url, options)
    );
  }

  performPost(
    url: string,
    body: any,
    needsAuthentication: boolean,
    contentType: string = 'application/json',
    headers?: HttpHeaders
  ): Observable<object> {
    const options: CustomRequestOptions = this.createOptions(
      headers,
      needsAuthentication,
      contentType
    );
    return this.performOperation(needsAuthentication, () =>
      this.http.post(url, body, options)
    );
  }

  performPut(
    url: string,
    body: any,
    needsAuthentication: boolean,
    contentType: string = 'application/json',
    headers?: HttpHeaders
  ): Observable<object> {
    const options: CustomRequestOptions = this.createOptions(
      headers,
      needsAuthentication,
      contentType
    );
    return this.performOperation(needsAuthentication, () =>
      this.http.put(url, body, options)
    );
  }

  performPutFormData(
    url: string,
    body: FormData,
    needsAuthentication: boolean,
    contentType: string = null,
    headers?: HttpHeaders
  ): Observable<object> {
    const options: CustomRequestOptions = this.createOptions(
      headers,
      needsAuthentication,
      contentType
    );
    return this.performOperation(needsAuthentication, () =>
      this.http.put(url, body, options)
    );
  }

  performPostFormDataWithProgress(
    url: string,
    body: FormData,
    needsAuthentication: boolean,
    contentType: string = null,
    headers?: HttpHeaders
  ): Observable<HttpEvent<any>> {
    const options = this.createOptions(
      headers,
      needsAuthentication,
      contentType,
      'arraybuffer',
      true
    );

    const req = new HttpRequest('POST', url, body, options);
    return this.http.request(req);
  }

  performDelete(
    url: string,
    needsAuthentication: boolean,
    contentType: string = 'application/json',
    headers?: HttpHeaders
  ): Observable<object> {
    const options: CustomRequestOptions = this.createOptions(
      headers,
      needsAuthentication,
      contentType
    );
    return this.performOperation(needsAuthentication, () =>
      this.http.delete(url, options)
    );
  }
}

export interface LoginResponse {
  token: string;
}

export interface AccountResponse {
  id: number;
  username: string;
  mail: string;
  roles: string[];
}

class TokenNotPresentException {}

interface ICustomRequestOptions {
  headers?;
  observe?;
  params?;
  reportProgress?;
  withCredentials?;
  responseType;
}

class CustomRequestOptions implements ICustomRequestOptions {
  headers?:
    | HttpHeaders
    | {
        [header: string]: string | string[];
      };
  observe?: 'body';
  params?:
    | HttpParams
    | {
        [param: string]: string | string[];
      };
  reportProgress?: boolean;
  withCredentials?: boolean;
  responseType: 'arraybuffer';
}

class CustomRequestOptionsText implements ICustomRequestOptions {
  headers?:
    | HttpHeaders
    | {
        [header: string]: string | string[];
      };
  observe?: 'body';
  params?:
    | HttpParams
    | {
        [param: string]: string | string[];
      };
  reportProgress?: boolean;
  withCredentials?: boolean;
  responseType: 'text';
}



