import { BaseDataSource } from '../../common/components/base-data-source';
import { IsoCodesService } from '../../services/iso-codes.service';
import { IsoCode } from '../../dao/iso-code';
import { Observable } from 'rxjs/Observable';
import { ListResults } from '../../common/services/crud.service';
import { AdvancedTableColumnFilter } from '../../common/components/advanced-table-data';

export class IsoCodesDataSource extends BaseDataSource<IsoCode> {
    constructor(private service: IsoCodesService) {
      super();
    }

    getData(
      filter: AdvancedTableColumnFilter[] = [],
      sortField: string = '',
      sortDirection: string = 'asc',
      pageIndex: number = 0,
      pageSize: number = 10
    ): Observable<ListResults<IsoCode>> {
      return this.service.getItems(
        filter,
        sortField,
        sortDirection,
        pageIndex,
        pageSize
      );
    }
  }



