import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, Router } from '@angular/router';

@Injectable()
export class HasAccessGuard implements CanActivate {

  constructor(private router: Router) { }

  canActivate(next: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean {

    const userInfo = JSON.parse(localStorage.getItem('user'))._value;
    if (userInfo && next.data.roles.includes(userInfo.roles[0].toLowerCase())) {
      return true;
    }
    // Redirect the user before denying them access to this route
    this.router.navigate(['unauthorized']);
    return false;
  }
}



