import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';

import { ApisService } from './apis.service';
import { ItemModelFieldMapper } from '../../dao/item-model-field-mapper';
import { CrudService } from './crud.service';

@Injectable()
export abstract class LockableCrudService<T extends object> extends CrudService<T> {

  constructor(protected apisService: ApisService, protected fieldMapper: ItemModelFieldMapper) {
    super(apisService, fieldMapper);
  }

  lockItem(id: number): Observable<any> {
    return this.lockUnlockItem(id, 1);
  }

  unlockItem(id: number): Observable<any> {
    return this.lockUnlockItem(id, 0);
  }

  private lockUnlockItem(id: number, lockStatus: number): Observable<any> {
    return this.apisService
      .performPut(
        this.apisService.baseApiUrl + '/lock',
        {
          id: id,
          resourceType: this.lockResourceType,
          locked: lockStatus,
        },
        true
      );
  }

  abstract get lockResourceType(): string;

  abstract get apiRoot(): string;
}



