import { Injectable } from '@angular/core';

import { IsoCode } from '../dao/iso-code';
import { ApisService } from '../common/services/apis.service';
import { IsoCodeFieldMapper } from '../dao/iso-code-field-mapper';
import { LockableCrudService } from '../common/services/lockable-crud.service';

@Injectable()
export class IsoCodesService extends LockableCrudService<IsoCode> {
  constructor(apisService: ApisService, fieldMapper: IsoCodeFieldMapper) {
    super(apisService, fieldMapper);
  }

  get apiRoot(): string {
    return 'IsoCodes';
  }

  get lockResourceType(): string {
    return 'isocode';
  }
}



