import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';

import { IsoCodesService } from '../../services/iso-codes.service';
import { IsoCode } from '../../dao/iso-code';
import { IsoCodesDataSource } from './iso-codes.datasource';
import { AdvancedTableData } from '../../common/components/advanced-table-data';
import { AdvancedTableComponent } from '../../common/components/advanced-table/advanced-table.component';
import { RoutingPaths } from '../../utils/routing-paths';
import { IsoCodeFieldMapper } from '../../dao/iso-code-field-mapper';

@Component({
  selector: 'app-iso-codes',
  templateUrl: './iso-codes.component.html',
  styleUrls: ['./iso-codes.component.scss']
})
export class IsoCodesComponent {
  listTitle = 'ISO Codes';
  @ViewChild('table') table: AdvancedTableComponent;
  advancedTableData: AdvancedTableData<IsoCode> = {
    columns: [
      {
        description: 'ISO Code',
        valueGetter: item => item.code,
        matColumnDefValue: this.fieldMapper.fieldMap['code']
      },
      {
        description: 'P Code',
        valueGetter: item => item.pcode,
        matColumnDefValue: this.fieldMapper.fieldMap['pcode']
      },
      {
        description: 'Description',
        valueGetter: item => item.description,
        matColumnDefValue: this.fieldMapper.fieldMap['description']
      }
    ],
    displayedColumns: [
      this.fieldMapper.fieldMap['isoCode'],
      this.fieldMapper.fieldMap['pcode'],
      this.fieldMapper.fieldMap['description']
    ],
    dataSource: new IsoCodesDataSource(this.crudService),
    actions: [
      {
        icon: 'edit',
        disabled: any => false,
        event: x => this.onEdit(x)
      },
      {
        icon: 'trash',
        disabled: any => false,
        event: x => this.onDelete(x)
      }
    ]
  };

  showDialog = false;
  dialogMessage: string;

  constructor(
    private router: Router,
    private crudService: IsoCodesService,
    private fieldMapper: IsoCodeFieldMapper
  ) {}

  onEdit(row: IsoCode): void {
    this.router.navigate([RoutingPaths.isoCodes, row.id]);
  }

  onDelete(row: IsoCode): void {
    this.crudService.lockItem(row.id).subscribe(
      item => {
        // if record is successfully locked => proceed to delete
        this.crudService.removeItem(row.id).subscribe(result => {
          this.table.items.splice(this.table.items.indexOf(row), 1);
        });
      },
      error => {
        if (error.status === 409) { // already locked
          this.dialogMessage = 'Record locked by another user!';
          this.showDialog = true;
        }
      }
    );
  }

  private addItem(): void {
    this.router.navigate([RoutingPaths.isoCodes, 'new']);
  }
}



