import { Component, ViewChild } from '@angular/core';
import { Router } from '@angular/router';

import { FailuresService } from '../../services/failures.service';
import { AdvancedTableData } from '../../common/components/advanced-table-data';
import { Failure } from '../../dao/failure';
import { FailuresDataSource } from './failures.datasource';
import { AdvancedTableComponent } from '../../common/components/advanced-table/advanced-table.component';
import { RoutingPaths } from '../../utils/routing-paths';
import { FailureFieldMapper } from '../../dao/failure-field-mapper';

@Component({
  selector: 'app-failures',
  templateUrl: './failures.component.html',
  styleUrls: ['./failures.component.scss']
})
export class FailuresComponent {
  listTitle = 'Failures';
  @ViewChild('table') table: AdvancedTableComponent;
  advancedTableData: AdvancedTableData<Failure> = {
    columns: [
      {
        description: 'Fault Name',
        valueGetter: item => item.name,
        matColumnDefValue: this.fieldMapper.fieldMap['name'],
        filterMatchMode: 'contains'
      },
      {
        description: 'Fault Title',
        valueGetter: item => item.faultTitle,
        matColumnDefValue: this.fieldMapper.fieldMap['faultTitle'],
        filterMatchMode: 'contains'
      },
      {
        description: 'Component',
        valueGetter: item => String(item.componentId), // TODO: Replace with the string value
        matColumnDefValue: this.fieldMapper.fieldMap['component'],
        filterMatchMode: 'equals'
      },
      {
        description: 'Failure Group',
        valueGetter: item => String(item.failureGroupId), // TODO: Replace with the string value
        matColumnDefValue: this.fieldMapper.fieldMap['failureGroupId'],
        filterMatchMode: 'equals'
      },
      {
        description: 'Responsible',
        valueGetter: item => String(item.responsibleId), // TODO: Replace with the string value
        matColumnDefValue: this.fieldMapper.fieldMap['responsible'],
        filterMatchMode: 'equals'
      },
      {
        description: 'Comment',
        valueGetter: item => item.comment,
        matColumnDefValue: this.fieldMapper.fieldMap['comment'],
        filterMatchMode: 'contains'
      },
      {
        description: 'Full Name',
        valueGetter: item => item.cameTitle, // TODO: Replace with the actual value
        matColumnDefValue: this.fieldMapper.fieldMap['cameTitle'],
        filterMatchMode: 'contains'
      },
      {
        description: 'Monitoring Strategy',
        valueGetter: item => item.monitorStrategyDescription,
        matColumnDefValue: this.fieldMapper.fieldMap['monitorStrategyDescription'],
        filterMatchMode: 'contains'
      },
      {
        description: 'CARB Component',
        valueGetter: item => String(item.carbComponentId), // TODO: Replace with the string value
        matColumnDefValue: this.fieldMapper.fieldMap['carbComponentId'],
        filterMatchMode: 'contains'
      },
      {
        description: 'Time Required',
        valueGetter: item => String(item.timeRequired),
        matColumnDefValue: this.fieldMapper.fieldMap['timeRequired'],
        filterMatchMode: 'contains'
      }
    ],
    displayedColumns: [
      this.fieldMapper.fieldMap['name'],
      this.fieldMapper.fieldMap['faultTitle'],
      this.fieldMapper.fieldMap['component'],
      this.fieldMapper.fieldMap['responsible'],
      this.fieldMapper.fieldMap['comment'],
      this.fieldMapper.fieldMap['fullName'],
      this.fieldMapper.fieldMap['monitorStrategyDescription'],
      this.fieldMapper.fieldMap['carbStandardizedComponent'],
      this.fieldMapper.fieldMap['timeRequired']
    ],
    dataSource: new FailuresDataSource(this.crudService),
    actions: [
      {
        icon: 'edit',
        disabled: any => false,
        event: x => this.onEdit(x)
      },
      {
        icon: 'trash',
        disabled: any => false,
        event: x =>
          this.onDelete(x)
      }
    ]
  };

  showDialog = false;
  dialogMessage: string;

  constructor(
    private router: Router,
    private crudService: FailuresService,
    private fieldMapper: FailureFieldMapper
  ) {}

  onEdit(row: Failure): void {
    this.router.navigate([RoutingPaths.failures, row.id]);
  }

  onDelete(row: Failure): void {
    this.crudService.lockItem(row.id).subscribe(
      item => {
        // if record is successfully locked => proceed to delete
        this.crudService.removeItem(row.id).subscribe(result => {
          this.table.items.splice(this.table.items.indexOf(row), 1);
        });
      },
      error => {
        if (error.status === 409) { // already locked
          this.dialogMessage = 'Record locked by another user!';
          this.showDialog = true;
        }
      }
    );
  }

  private addItem(): void {
    this.router.navigate([RoutingPaths.failures, 'new']);
  }
}



