import { Injectable } from '@angular/core';

import { User } from '../dao/user';
import { ApisService } from '../common/services/apis.service';
import { UserFieldMapper } from '../dao/user-field-mapper';
import { CrudService } from '../common/services/crud.service';

@Injectable()
export class UsersService extends CrudService<User> {
  constructor(apisService: ApisService, fieldMapper: UserFieldMapper) {
    super(apisService, fieldMapper);
  }

  get apiRoot(): string {
    return 'Users';
  }
}



