import { ItemModelFieldMapper } from './item-model-field-mapper';

export class ProjectFieldMapper implements ItemModelFieldMapper {
  readonly fieldMap = {
    id: 'id',
    version: 'version',
    name: 'name',
    project: 'project',
    revision: 'revision',
    ecu: 'ecu',
    dsmStructure: 'dsmStructure',
    comment: 'comment',
    status: 'status',
  };
}



