import { Component, ViewChild } from '@angular/core';
import { Router } from '@angular/router';

import { AdvancedTableData } from '../../common/components/advanced-table-data';
import { PerformanceLimiterCalibrationService } from '../../services/performance-limiter-calibration.service';
import { PerformanceLimiterCalibration } from '../../dao/performance-limiter-calibration';
import { PerformanceLimiterCalibrationDataSource } from './performance-limiter-calibrations.datasource';
import { AdvancedTableComponent } from '../../common/components/advanced-table/advanced-table.component';
import { RoutingPaths } from '../../utils/routing-paths';
import { PerformanceLimiterCalibrationFieldMapper } from '../../dao/performance-limiter-calibration-field-mapper';
import { Project } from '../../dao/project';
import { FidReactions } from '../../dao/fid-reaction';

@Component({
  selector: 'app-performance-limiter-calibrations',
  templateUrl: './performance-limiter-calibrations.component.html',
  styleUrls: ['./performance-limiter-calibrations.component.scss']
})
export class PerformanceLimiterCalibrationsComponent {
  listTitle = 'Performance Limiter Calibration';
  @ViewChild('table') table: AdvancedTableComponent;
  advancedTableData: AdvancedTableData<PerformanceLimiterCalibration> = {
    columns: [
      {
        description: 'FID',
        valueGetter: item => item.fid,
        matColumnDefValue: this.fieldMapper.fieldMap['fid'],
        filterMatchMode: 'contains'
      },
      {
        description: 'Reaction',
        valueGetter: item => FidReactions.getFidReactionById(item.reactionId).name,
        matColumnDefValue: this.fieldMapper.fieldMap['reactionId'],
        filterMatchMode: 'contains'
      },
      {
        description: 'In Project',
        valueGetter: item => item.projects.map((project: Project) => project.project + project.version).join(' | '),
        matColumnDefValue: this.fieldMapper.fieldMap['projects'],
        filterMatchMode: 'contains'
      },
    ],
    displayedColumns: [
      this.fieldMapper.fieldMap['fid'],
      this.fieldMapper.fieldMap['reactionId'],
      this.fieldMapper.fieldMap['projects'],
    ],
    dataSource: new PerformanceLimiterCalibrationDataSource(this.crudService),
    actions: [
      {
        icon: 'edit',
        disabled: any => false,
        event: x => this.onEdit(x)
      },
      {
        icon: 'trash',
        disabled: any => false,
        event: x => this.onDelete(x)
      }
    ]
  };

  showDialog = false;
  dialogMessage: string;

  constructor(
    private router: Router,
    private crudService: PerformanceLimiterCalibrationService,
    private fieldMapper: PerformanceLimiterCalibrationFieldMapper
  ) {}

  onEdit(row: PerformanceLimiterCalibration): void {
    this.router.navigate([RoutingPaths.performanceLimiterCalibrations, row.id]);
  }

  onDelete(row: PerformanceLimiterCalibration): void {
    this.crudService.lockItem(row.id).subscribe(
      item => {
        // if record is successfully locked => proceed to delete
        this.crudService.removeItem(row.id).subscribe(result => {
          this.table.items.splice(this.table.items.indexOf(row), 1);
        });
      },
      error => {
        if (error.status === 409) { // already locked
          this.dialogMessage = 'Record locked by another user!';
          this.showDialog = true;
        }
      }
    );
  }

  private addItem(): void {
    this.router.navigate([RoutingPaths.performanceLimiterCalibrations, 'new']);
  }
}



