import { Injectable } from '@angular/core';

import { ChannelDescription } from '../dao/channel-description';
import { ApisService } from '../common/services/apis.service';
import { ChannelDescriptionFieldMapper } from '../dao/channel-description-field-mapper';
import { LockableCrudService } from '../common/services/lockable-crud.service';

@Injectable()
export class ChannelDescriptionsService extends LockableCrudService<ChannelDescription> {
  constructor(apisService: ApisService, fieldMapper: ChannelDescriptionFieldMapper) {
    super(apisService, fieldMapper);
  }

  get apiRoot(): string {
    return 'ChannelDescriptions';
  }

  get lockResourceType(): string {
    return 'channeldescription';
  }
}



