import { ItemModelFieldMapper } from './item-model-field-mapper';

export class UserFieldMapper implements ItemModelFieldMapper {
  readonly fieldMap = {
    id: 'id',
    username: 'username',
    mail: 'mail',
    roles: 'roles',
    roleId: 'roleId',
    role: 'role',
  };
}



