import { Injectable } from '@angular/core';
import { HttpEvent } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';

import { ApisService } from '../common/services/apis.service';

@Injectable()
export class UploadService {
  constructor(private apisService: ApisService) {}

  getUploadA2LUrl(): string {
    return this.apisService.baseApiUrl + '/uploadfiles/a2l';
  }

  getUploadHexUrl(): string {
    return this.apisService.baseApiUrl + '/uploadfiles/hex';
  }

  uploadA2L(formData: FormData) {
    return this.upload(formData, this.getUploadA2LUrl());
  }

  uploadHex(formData: FormData) {
    return this.upload(formData, this.getUploadHexUrl());
  }

  parseUploadedFiles(data: object) {
    return this.apisService.performPut(
      this.apisService.baseApiUrl + '/upload',
      data,
      false,
      'application/json'
    );
  }

  parseUploadedFileToAddProject(data: object) {
    return this.apisService.performPost(
      this.apisService.baseApiUrl + '/upload',
      data,
      false,
      'application/json'
    );
  }

  private upload(formData: FormData, url: string): Observable<HttpEvent<any>> {
    return this.apisService.performPostFormDataWithProgress(
      url,
      formData,
      false
    );
  }
}



