import { Component, OnInit, OnChanges, SimpleChanges } from '@angular/core';
import { Router } from '@angular/router';
import { FormGroup, Validators, FormControl } from '@angular/forms';
import { HttpEventType, HttpResponse } from '@angular/common/http';

import { Project } from '../../dao/project';
import { ProjectsService } from '../../services/projects.service';
import { SelectOption } from '../../common/components/dynamic-forms/field-definition';
import { ProjectFieldMapper } from '../../dao/project-field-mapper';
import { UploadService } from '../../services/upload.service';

@Component({
  selector: 'app-edit-project',
  templateUrl: './edit-project.component.html',
  styleUrls: ['./edit-project.component.scss']
})
export class EditProjectComponent implements OnInit, OnChanges {
  selectedProject: Project;
  previousProject: Project;
  projectOptions: SelectOption[];
  action = 'add';
  form: FormGroup;

  showDialog = false;
  dialogShowHeader = true;
  dialogHeaderClose = false;
  dialogHeaderTitle: string;
  dialogMessage: string;
  dialogShowProgress = false;
  dialogShowProgressPercentage = false;

  selectedA2L: File;
  showLockDialog: boolean;
  lockDialogMessage: string;
  protected lockedByCurrentUser = false;
  a2lUploaded = false;
  uploadPercentage = 0;
  a2lServerPath: string;
  uploadedA2lFileName: string;

  constructor(
    private router: Router,
    private dataService: ProjectsService,
    private fieldMapper: ProjectFieldMapper,
    private uploadService: UploadService,
  ) { }

  ngOnInit() {
    this.setProjectOptions();
    this.resetForm();
  }

  private setProjectOptions() {
    this.projectOptions = [];
    this.dataService
      .getItems([], this.fieldMapper.fieldMap.name, 'asc', null, null)
      .subscribe(result => {
        result.data.map((project: Project) => {
          this.projectOptions.push({value: project.id.toString(), label: project.name});
        });
      }
    );
  }

  resetForm() {
    this.form = new FormGroup({
      action: new FormControl(this.action, Validators.required),
      selectedProject: new FormControl('selectedProject', Validators.required),
      previousProject: new FormControl('previousProject', Validators.required),
      selectedA2L: new FormControl(this.selectedA2L)
    });
  }

  ngOnChanges(changes: SimpleChanges) {
    this.resetForm();
  }

  private cancel() {
    // TODO: home or back?
    this.router.navigate(['home']);
  }

  private save() {
    if (this.action === 'add' && (!this.form.value.selectedProject || !this.form.value.previousProject || !this.a2lServerPath)) {
      this.showMissingValuesError(
        '"Selected EDC Project" and "Previous EDC Project" are mandatory, and an A2L file needs to be previously uploaded!'
      );
      return;
    } else if (this.action === 'remove' && (!this.form.value.selectedProject)) {
      this.showMissingValuesError('"Selected EDC Project" is mandatory!');
      return;
    }

    this.dataService.lockItem(this.form.value.selectedProject).subscribe(
      item => {
        // if record is successfully locked...
        this.lockedByCurrentUser = true;
        if (this.action === 'add') {
          this.add();
        } else {
          this.remove();
        }
      },
      error => {
        if (error.status === 409) { // already locked
          this.lockedByCurrentUser = false;
          this.dialogShowProgress = false;
          this.dialogHeaderTitle = 'Info';
          this.dialogHeaderClose = true;
          this.dialogMessage = '"Selected EDC Project" is locked by another user!';
          setTimeout(() => this.showDialog = true);
        }
      }
    );
  }

  private add() {
    this.dialogShowHeader = false;
    this.dialogMessage = 'Add operation in progress...';
    this.dialogShowProgress = true;
    this.dialogShowProgressPercentage = false;
    this.showDialog = true;

    const requestData = {
      projectNewId: this.form.value.selectedProject,
      projectPreviousId: this.form.value.previousProject,
      pathA2l: this.a2lServerPath,
    };

    this.uploadService.parseUploadedFileToAddProject(requestData).subscribe(
      (result: any) => {
        console.log(result);
        this.showDialog = false;
        if (!this.lockedByCurrentUser) {
          return true;
        }
        this.lockDialogMessage = 'Operation successfully finished! You have locked the "Selected EDC Project", '
          + 'which prevents other users from editing it! What would you like to do?';
        this.showLockDialog = true;
      },
      error => {
        console.log(error);
        this.showDialog = false;
        if (!this.lockedByCurrentUser) {
          return true;
        }
        this.lockDialogMessage = 'An error has occurred! You have locked the "Selected EDC Project", '
          + 'which prevents other users from editing it! What would you like to do?';
        this.showLockDialog = true;
      }
    );
  }

  private remove() {
    this.dataService.removeItem(this.form.value.selectedProject).subscribe(result => {
      // remove it from dropdowns options
      const optionIndex = this.projectOptions.findIndex(option => option.value === this.form.value.selectedProject);
      if (optionIndex !== -1) {
        this.projectOptions.splice(optionIndex, 1);
      }
      this.resetForm();
    });
  }

  onFileChange(event) {
    if (event.files.length > 0) {
      this.form.get('selectedA2L').setValue(event.files[0]);
    }
  }

  uploadA2L(event) {
    if (this.a2lUploaded) {
      alert('File already uploaded');
      return;
    }

    this.dialogShowHeader = true;
    this.dialogHeaderClose = false;
    this.dialogHeaderTitle = 'Operation in progress';
    this.dialogMessage = 'Uploading Selected A2L file to server...';
    this.dialogShowProgress = true;
    this.dialogShowProgressPercentage = true;
    this.showDialog = true;

    const formData = new FormData();
    formData.append('file', this.form.value.selectedA2L);

    this.uploadPercentage = 0;
    this.uploadService.uploadA2L(formData).subscribe(
      (result: any) => {
        if (result.type === HttpEventType.UploadProgress) {
          this.uploadPercentage = Math.round(100 * result.loaded / result.total);
        } else if (result instanceof HttpResponse) {
          if (result.body.isStoredSuccessfully) {
            this.a2lUploaded = true;
            this.a2lServerPath = result.body.path;
            this.uploadedA2lFileName = this.form.value.selectedA2L.name;
          } else {
            this.showFileStoringError();
          }
        }
      },
      error => {
        this.showFileStoringError();
        console.log(error);
      },
      () => {
        this.showDialog = false;
        this.uploadPercentage = 0;
      }
    );
  }

  formatBytes(bytes, decimals) {
    if (bytes === 0) {
      return '0 Bytes';
    }
    const k = 1024;
    const dm = decimals || 2;
    const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + ' ' + sizes[i];
  }

  dialogKeepLock() {
    this.showLockDialog = false;
    this.lockedByCurrentUser = true;
  }

  dialogUnlock() {
    this.showLockDialog = false;
    this.dataService.unlockItem(this.form.value.selectedProject).subscribe(
      success => {
        this.lockedByCurrentUser = false;
      },
      error => {
        console.log('error unlocking');
      },
    );
  }

  private showFileStoringError() {
    this.dialogShowHeader = true;
    this.dialogHeaderClose = true;
    this.dialogHeaderTitle = 'Error';
    this.dialogMessage = 'There was an error storing the file on server! Please re-upload it!';
    this.dialogShowProgress = false;
    this.dialogShowProgressPercentage = false;
    this.showDialog = true;
  }

  private showMissingValuesError(msg: string) {
    this.dialogShowHeader = true;
    this.dialogHeaderClose = true;
    this.dialogHeaderTitle = 'Error';
    this.dialogMessage = msg;
    this.dialogShowProgress = false;
    this.dialogShowProgressPercentage = false;
    this.showDialog = true;
  }
}



