import { BaseDataSource } from '../../common/components/base-data-source';
import { PerformanceLimiterCalibration } from '../../dao/performance-limiter-calibration';
import { PerformanceLimiterCalibrationService } from '../../services/performance-limiter-calibration.service';
import { Observable } from 'rxjs/Observable';
import { ListResults } from '../../common/services/crud.service';
import { AdvancedTableColumnFilter } from '../../common/components/advanced-table-data';

export class PerformanceLimiterCalibrationDataSource extends BaseDataSource<PerformanceLimiterCalibration> {
    constructor(private performanceLimiterCalibrationsService: PerformanceLimiterCalibrationService) {
      super();
    }

    getData(
      filter: AdvancedTableColumnFilter[] = [],
      sortField: string = '',
      sortDirection: string = 'asc',
      pageIndex: number = 0,
      pageSize: number = 10
    ): Observable<ListResults<PerformanceLimiterCalibration>> {
      return this.performanceLimiterCalibrationsService.getItems(
        filter,
        sortField,
        sortDirection,
        pageIndex,
        pageSize
      );
    }
  }



