import { ItemModelFieldMapper } from './item-model-field-mapper';

export class LabelDescriptionFieldMapper implements ItemModelFieldMapper {
  readonly fieldMap = {
    id: 'id',
    name: 'name',
    unit: 'unit',
    description: 'description',
    comment: 'comment',
    projectId: 'projectId',
    timestamp: 'ssmaTimeStamp'
  };
}



