import { Component, OnInit, OnChanges, SimpleChanges } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { FormGroup, Validators, FormControl } from '@angular/forms';

import { Failure } from '../../dao/failure';
import { FailureFieldMapper } from '../../dao/failure-field-mapper';
import { FailuresService } from '../../services/failures.service';

@Component({
  selector: 'app-copy-failures-list',
  templateUrl: './copy-failures-list.component.html',
  styleUrls: ['./copy-failures-list.component.scss']
})
export class CopyFailuresListComponent implements OnInit, OnChanges {
  failures: Failure[];
  filteredFailures: Failure[];
  selectedFailures: Failure[];
  comment: string;
  form: FormGroup;
  showDialog = false;
  dialogShowProgress = false;
  dialogShowHeader = true;
  dialogHeaderTitle: string;
  dialogMessage: string;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private dataService: FailuresService,
    private fieldMapper: FailureFieldMapper,
  ) { }

  ngOnInit() {
    this.getAllFailures();
    this.resetForm();
  }

  resetForm() {
    // legacy app defaults
    this.comment = 'machined copied by ListFunction';

    this.form = new FormGroup({
      selectedFailures: new FormControl('selectedFailures', Validators.required),
      comment: new FormControl('comment'),
    });
  }

  ngOnChanges(changes: SimpleChanges) {
    this.resetForm();
  }

  private cancel() {
    // TODO: home or back?
    this.router.navigate(['home']);
  }

  private submit(event) {
    this.dialogShowHeader = false;
    this.dialogMessage = 'Operation in progress...';
    this.dialogShowProgress = true;
    this.showDialog = true;

    const formValues = this.form.value;
    this.dataService.copyFailures(formValues.selectedFailures, formValues.comment).subscribe(
      result => {
        this.showDialog = false;
        this.dialogShowProgress = false;
        this.dialogShowHeader = true;
        this.dialogHeaderTitle = 'Info';
        this.dialogMessage = this.selectedFailures.length + ' failure'
          + (this.selectedFailures.length > 1 ? 's' : '') + ' successfully copied!';
        this.showDialog = true;
        this.filteredFailures = [];
        this.selectedFailures = [];
        this.resetForm();
      },
      error => {
        this.showDialog = false;
        this.dialogShowProgress = false;
        this.dialogShowHeader = true;
        this.dialogHeaderTitle = 'Error';
        this.dialogMessage = 'An error has occurred!';
        this.showDialog = true;
      }
    );
  }

  private getAllFailures() {
    this.dataService
      .getItems([], '', '', null, null)
      .subscribe(result => this.failures = result.data);
  }

  private filterFailures(str: string) {
    const upperStr = str.toUpperCase();
    this.filteredFailures = this.failures.filter(failure => (
      failure[this.fieldMapper.fieldMap['name']].toUpperCase().indexOf(upperStr) >= 0
        || failure[this.fieldMapper.fieldMap['id']].toString().indexOf(upperStr) >= 0
    ));
  }
}



