import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';

import { ApisService } from '../common/services/apis.service';
import { LabelDescriptionCheck } from '../dao/label-description-check';

@Injectable()
export class LabelDescriptionCheckService {
  // constructor(apisService: ApisService, fieldMapper: LabelDescriptionCheckFieldMapper) {}
  constructor(private apisService: ApisService) {}

  get apiRoot(): string {
    return 'LabelDescriptions/check';
  }

  check(requestParams: LabelDescriptionCheckRequest): Observable<LabelDescriptionCheck[]> {
    const url: string = this.apisService.baseApiUrl + '/' + this.apiRoot + requestParams.getQueryString();
    return this.apisService.performGet(url, true).map(
      result => {
        return <LabelDescriptionCheck[]>result;
      }
    );
  }

  getTxtReport(requestParams: LabelDescriptionCheckRequest): any {
    const url: string = this.apisService.baseApiUrl + '/' + this.apiRoot  + '/reportTXT' + requestParams.getQueryString();
    return this.apisService.performGetText(url, true, 'text');
  }
}

export class LabelDescriptionCheckRequest {
  projectId?: number;
  generalRemark = true;
  testCondition = true;
  faultDetection = true;
  defectDebouncing = true;
  faultHealing = true;
  healingDebouncing = true;
  systemReaction = true;
  faultDisabling = true;
  faultCause = true;
  faultRepair = true;

  public getQueryString(): string {
    return '?GeneralRemark=' + this.generalRemark +
      '&TestCondition=' + this.testCondition +
      '&FaultDetection=' + this.faultDetection +
      '&DefectDebouncing=' + this.defectDebouncing +
      '&FaultHealing=' + this.faultHealing +
      '&HealingDebouncing=' + this.healingDebouncing +
      '&SystemReaction=' + this.systemReaction +
      '&FaultDisabling=' + this.faultDisabling +
      '&FaultCause=' + this.faultCause +
      '&FaultRepair=' + this.faultRepair +
      (this.projectId ? '&ProjectId=' + this.projectId : '')
    ;
  }
}



