import { Injectable } from '@angular/core';

import { Project } from '../dao/project';
import { ApisService } from '../common/services/apis.service';
import { ProjectFieldMapper } from '../dao/project-field-mapper';
import { LockableCrudService } from '../common/services/lockable-crud.service';

@Injectable()
export class ProjectsService extends LockableCrudService<Project> {
  constructor(apisService: ApisService, fieldMapper: ProjectFieldMapper) {
    super(apisService, fieldMapper);
  }

  get apiRoot(): string {
    return 'Projects';
  }

  get lockResourceType(): string {
    return 'project';
  }

  editProject(projectId: number, formData: FormData) {
    return this.apisService.performPutFormData(
      this.apisService.baseApiUrl + '/' + this.apiRoot + '/Edit/' + projectId,
      formData,
      true
    );
  }
}



