import { ItemModelFieldMapper } from './item-model-field-mapper';

export class ChannelDescriptionFieldMapper implements ItemModelFieldMapper {
  readonly fieldMap = {
    id: 'id',
    name: 'name',
    description: 'description',
    boschDescription: 'boschDescription',
    comment: 'comment',
    projectCode: 'projectCode',
    projectId: 'projectId',
    timeStamp: 'ssmaTimeStamp',
    status: 'status',
  };
}



