import { BaseDataSource } from '../../common/components/base-data-source';
import { Project } from '../../dao/project';
import { ProjectsService } from '../../services/projects.service';
import { Observable } from 'rxjs/Observable';
import { ListResults } from '../../common/services/crud.service';
import { AdvancedTableColumnFilter } from '../../common/components/advanced-table-data';

export class ProjectsDataSource extends BaseDataSource<Project> {
    constructor(private projectsService: ProjectsService) {
      super();
    }

    getData(
      filter: AdvancedTableColumnFilter[] = [],
      sortField: string = '',
      sortDirection: string = 'asc',
      pageIndex: number = 0,
      pageSize: number = 10
    ): Observable<ListResults<Project>> {
      return this.projectsService.getItems(
        filter,
        sortField,
        sortDirection,
        pageIndex,
        pageSize
      );
    }
  }



