import { Injectable } from '@angular/core';

import { PerformanceLimiterCalibration } from '../dao/performance-limiter-calibration';
import { ApisService } from '../common/services/apis.service';
import { PerformanceLimiterCalibrationFieldMapper } from '../dao/performance-limiter-calibration-field-mapper';
import { LockableCrudService } from '../common/services/lockable-crud.service';

@Injectable()
export class PerformanceLimiterCalibrationService extends LockableCrudService<PerformanceLimiterCalibration> {
  constructor(apisService: ApisService, fieldMapper: PerformanceLimiterCalibrationFieldMapper) {
    super(apisService, fieldMapper);
  }

  get apiRoot(): string {
    return 'FIDReactions';
  }

  get lockResourceType(): string {
    return 'fidreaction';
  }
}



