import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { AdvancedTableColumnFilter } from '../../common/components/advanced-table-data';
import { Failure } from '../../dao/failure';
import { Router } from '@angular/router';
import { FailuresService } from '../../services/failures.service';
import { FailureFieldMapper } from '../../dao/failure-field-mapper';
import { FailuresDataSource } from '../failures/failures.datasource';
import { ProjectsService } from '../../services/projects.service';
import { ProjectFieldMapper } from '../../dao/project-field-mapper';

const defaultNumberOfProjects = 30;

@Component({
  selector: 'app-failures-projects',
  templateUrl: './failures-projects.component.html',
  styleUrls: ['./failures-projects.component.scss']
})
export class FailuresProjectsComponent implements OnInit {
  @ViewChild('containerDiv') containerDiv: ElementRef;
  listTitle = 'Failures <-> Projects';
  items: any[] = [];
  itemsOriginal: any[] = [];
  columns = [
    {
      header: 'Name',
      field: this.fieldMapper.fieldMap.name,
      filterType: 'text',
      filterMatchMode: 'contains',
      filterOptions: [],
    },
    {
      header: 'Title',
      field: this.fieldMapper.fieldMap.faultTitle,
      filterType: 'text',
      filterMatchMode: 'contains',
      filterOptions: [],
    },
  ];

  pageSize = 50;
  rowsPerPageOptions: number[] = [10, 20, 30, 50, 100];
  totalRecords: number;
  showDialog = false;
  dialogMessage: string;
  dialogShowHeader = true;
  dialogHeaderTitle: string;
  loading = true;
  tableWidth: string;

  dataSource = new FailuresDataSource(this.dataService);
  dirtyFailureIds: number[] = [];

  allProjects = [];
  tableProjects = [];
  selectedProjectsTemp = [];
  showProjectSelectionDialog = false;
  loadingAllProjects = false;
  projectSelectionColumns: any[] = [
    {
      header: 'Project',
      field: this.projectFieldMapper.fieldMap['project'],
      filterMatchMode: 'contains'
    },
    {
      header: 'Version',
      field: this.projectFieldMapper.fieldMap['version'],
      filterMatchMode: 'contains'
    },
  ];

  constructor(
    private router: Router,
    private dataService: FailuresService,
    private fieldMapper: FailureFieldMapper,
    private projectsDataService: ProjectsService,
    private projectFieldMapper: ProjectFieldMapper
  ) {}

  ngOnInit() {
    this.tableWidth = this.containerDiv.nativeElement.clientWidth + 'px';

    // get all projects
    const projectFields = [
      this.projectFieldMapper.fieldMap.id,
      this.projectFieldMapper.fieldMap.project,
      this.projectFieldMapper.fieldMap.version
    ];
    this.projectsDataService
      .getItems([], this.projectFieldMapper.fieldMap.project, 'asc', null, null, projectFields)
      .subscribe(result => {
        this.allProjects = result.data.sort(
          function(a, b) {
            return a.project.localeCompare(b.project) || a.version.localeCompare(b.version);
          }
        );
      },
      error => {},
      () => {
        this.tableProjects = this.allProjects.slice(0, defaultNumberOfProjects).map(project => {
          return { id: project.id, project: project.project, version: project.version };
        });
      }
    );
  }

  lazyLoadData(event) {
    this.getItems(event);
  }

  getItems(event): void {
    this.loading = true;
    const filters = [];
    let sortField = '';
    let sortOrder = '';
    let pageIndex = 0;

    if (event) {
      this.pageSize = event.rows;

      Object.keys(event.filters).forEach(field => {
        filters.push(new AdvancedTableColumnFilter(field, event.filters[field].value, event.filters[field].matchMode));
      });

      sortField = event.sortField;
      sortOrder = event.sortOrder === 1 ? 'asc' : 'desc';
      pageIndex = event.first / this.pageSize;
    }

    // get failures
    const failureFields = [
      this.fieldMapper.fieldMap.id,
      this.fieldMapper.fieldMap.name,
      this.fieldMapper.fieldMap.faultTitle,
      this.fieldMapper.fieldMap.projects
    ];
    this.dataSource.getData(filters, sortField, sortOrder, pageIndex, this.pageSize, failureFields).subscribe(
      result => {
        this.items = result.data.map(
          (failure: Failure) => {
            return {
              id: failure[this.fieldMapper.fieldMap.id],
              name: failure[this.fieldMapper.fieldMap.name],
              title: failure[this.fieldMapper.fieldMap.faultTitle],
              projects: failure[this.fieldMapper.fieldMap.projects],
            };
          }
        );
      },
      error => { },
      () => {
        this.itemsOriginal = [];
        this.items.map(obj => { this.itemsOriginal.push(Object.assign({}, obj)); });
        this.totalRecords = this.items.length;
        this.loading = false;
      }
    );
  }

  private checkboxChanged(failureId: number, checked: boolean) {

    const idxOriginal = this.itemsOriginal.findIndex(failure => failure.id === failureId);
    if (idxOriginal < 0) {
      return;
    }
    let idx = this.items.findIndex(failure => failure.id === failureId);
    if (idx < 0) {
      return;
    }

    const equalProjects = this.projectArraysEqual(this.items[idx].projects, this.itemsOriginal[idxOriginal].projects);

    // add failureId to array if it doesn't exist already
    if (this.dirtyFailureIds.indexOf(failureId) < 0 && !equalProjects) {
      this.dirtyFailureIds.push(failureId);
    } else {
      // remove if it's there and projects are equal to original
      idx = this.dirtyFailureIds.indexOf(failureId);
      if (idx > -1 && equalProjects) {
        this.dirtyFailureIds.splice(idx, 1);
      }
    }
  }

  private projectArraysEqual(arr1, arr2): boolean {
    if (!Array.isArray(arr1) || !Array.isArray(arr2) || arr1.length !== arr2.length) {
      return false;
    }

    const a1 = arr1.concat().sort();
    const a2 = arr2.concat().sort();

    for (let i = 0; i < a1.length; i++) {
      if (a1[i] !== a2[i]) {
        return false;
      }
    }

    return true;
  }

  private discardChanges() {
    this.loading = true;
    this.items = [];
    this.dirtyFailureIds = [];
    this.itemsOriginal.map(obj => { this.items.push(Object.assign({}, obj)); });
    this.loading = false;
  }

  private saveChanges() {
    const uniqueDirtyFailureIds = [...Array.from(new Set(this.dirtyFailureIds))];

    const dirtyFailures = [];
    this.items.map(failure => {
      if (uniqueDirtyFailureIds.findIndex(id => id === failure.id) > -1) {
        dirtyFailures.push({
          [this.fieldMapper.fieldMap.id]: failure.id,
          [this.fieldMapper.fieldMap.projects]: failure.projects
        });
      }
    });

    this.dataService.updateFailuresProjects(dirtyFailures).subscribe(
      result => {
        this.showDialog = false;
        this.dialogHeaderTitle = 'Info';
        this.dialogMessage = dirtyFailures.length + ' failure' + (dirtyFailures.length > 1 ? 's' : '') + ' successfully updated!';
        this.showDialog = true;

        this.dirtyFailureIds = [];
        this.itemsOriginal = [];
        this.items.map(obj => { this.itemsOriginal.push(Object.assign({}, obj)); });
      },
      error => {
        this.showDialog = false;
        this.dialogShowHeader = true;
        this.dialogHeaderTitle = 'Error';
        this.dialogMessage = 'An error has occurred!';
        this.showDialog = true;
      }
    );
  }

  private cancel() {
    // TODO: home or back?
    this.router.navigate(['home']);
  }

  private openProjectSelectionDialog() {
    this.selectedProjectsTemp = this.tableProjects;
    this.showProjectSelectionDialog = true;
  }

  private closeProjectSelectionDialog() {
    this.showProjectSelectionDialog = false;
    this.selectedProjectsTemp = [];
  }

  private saveProjectSelectionDialog() {
    this.tableProjects = this.selectedProjectsTemp.map(project => {
      return { id: project.id, project: project.project, version: project.version };
    });
    this.closeProjectSelectionDialog();
  }
}



