import { ItemModelFieldMapper } from './item-model-field-mapper';

export class IsoCodeFieldMapper implements ItemModelFieldMapper {
  readonly fieldMap = {
    id: 'id',
    code: 'code',
    pcode: 'pcode',
    description: 'description',
    comment: 'comment',
    status: 'status',
  };
}



