import { Component, Input, Output, EventEmitter, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { FailuresService } from '../../../services/failures.service';
import { Failure } from '../../../dao/failure';
import { ChannelDescriptionsService } from '../../../services/channel-descriptions.service';
import { ChannelDescriptionFieldMapper } from '../../../dao/channel-description-field-mapper';
import { ChannelDescription } from '../../../dao/channel-description';

@Component({
  selector: 'app-carb-fid',
  templateUrl: './carb-fid.component.html',
  styleUrls: ['./carb-fid.component.scss']
})
export class CarbFidComponent implements OnInit {
  @Input() visibleIn = false;
  @Output() visibleOut: EventEmitter<boolean> = new EventEmitter<boolean>();
  @Input() failure: Failure;

  failureId: number;
  carbFidOriginalList: CarbFid[] = [];
  carbFidList: CarbFid[] = [];
  carbFidOptions: any[];
  isDirty = false;

  constructor(
    private route: ActivatedRoute,
    private dataService: FailuresService,
    private channelDescriptionsDataService: ChannelDescriptionsService,
    private channelDescriptionsFieldMapper: ChannelDescriptionFieldMapper,
  ) { }

  ngOnInit(): void {
    this.failureId = this.route.snapshot.params['id'];
    this.getCarbFids(this.failureId);
    this.getAllCarbFids();
  }

  getCarbFids(failureId: number) {
    this.dataService.getCarbFids(failureId).subscribe(
      result => {
        result.map((item) => {
          const itemAsCarbFid = { id: item.fidReactions.id, name: item.fidReactions.fid };
          // avoid duplicates on fid (because the endpoint is returning them!)
          if (!this.carbFidOriginalList.find(carbFid => carbFid.name === itemAsCarbFid.name)) {
            this.carbFidOriginalList.push(itemAsCarbFid);
          }
        });
        // this.carbFidOriginalList = <CarbFid[]>result['carbFids'];
        this.carbFidList = JSON.parse(JSON.stringify(this.carbFidOriginalList));
      }
    );
  }

  getAllCarbFids() {
    this.carbFidOptions = [];
    this.channelDescriptionsDataService
      .getItems(
        [{field: this.channelDescriptionsFieldMapper.fieldMap.name, value: 'FId_', matchMode: 'startsWith'}],
        this.channelDescriptionsFieldMapper.fieldMap.name,
        'asc',
        null,
        null
      )
      .subscribe(result => {
        result.data.map((fid: ChannelDescription) => {
          this.carbFidOptions.push({value: fid.name, label: fid.name});
        });
      })
    ;
  }

  // private onChange(event: any) {
  //   this.isDirty = true;
  //   console.log(event);
  //   console.log(this.carbFidList);
  // }

  close() {
    if (this.isDirty) { // show warning before restoring the list?
      // restore carbFidList to original
      this.carbFidList = JSON.parse(JSON.stringify(this.carbFidOriginalList));
      this.isDirty = false;
    }

    this.visibleIn = false;
    this.visibleOut.emit(false);
  }

  saveCarbFids() {
    const carbFidRequest = {
      id: this.failureId,
      carbFids: this.carbFidList,
    };

    this.dataService.updateCarbFids(this.failureId, carbFidRequest).subscribe(
      result => {
        this.carbFidOriginalList = JSON.parse(JSON.stringify(this.carbFidList));
        this.isDirty = false;
        this.close();
      },
      error => { alert('An error has occurred!'); console.log(error); }
    );
  }
}

class CarbFid {
  id: number;
  name: string;
}



