import { ChannelDescription } from '../../dao/channel-description';
import { BaseDataSource } from '../../common/components/base-data-source';
import { ChannelDescriptionsService } from '../../services/channel-descriptions.service';
import { Observable } from 'rxjs/Observable';
import { ListResults } from '../../common/services/crud.service';
import { AdvancedTableColumnFilter } from '../../common/components/advanced-table-data';

export class ChannelDescriptionsDataSource extends BaseDataSource<ChannelDescription> {
constructor(private service: ChannelDescriptionsService) {
  super();
}

getData(
  filter: AdvancedTableColumnFilter[] = [],
  sortField: string = '',
  sortDirection: string = 'asc',
  pageIndex: number = 0,
  pageSize: number = 10
): Observable<ListResults<ChannelDescription>> {
  return this.service.getItems(
    filter,
    sortField,
    sortDirection,
    pageIndex,
    pageSize
  );
}
}



