import { Component, OnInit } from '@angular/core';
import { RoutingPaths } from '../../../utils/routing-paths';
import { AccountService } from '../../../services/account.service';
import { AccountResponse } from '../../services/apis.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {

  userInfo: AccountResponse = null;

  protected menu: MenuItem[] = [
    {
      label: 'Home',
      href: RoutingPaths.home,
      displayForRoles: ['guest', 'admin'],
    },
    {
      label: 'Create',
      href: RoutingPaths.create,
      displayForRoles: ['admin'],
    },
    {
      label: 'CARB Create',
      href: RoutingPaths.home, // TODO
      displayForRoles: ['admin'],
      disabled: true,
    },
    {
      label: 'Data',
      displayForRoles: ['admin'],
      subMenu: [
        {
          label: 'Projects',
          href: RoutingPaths.projects,
          displayForRoles: ['admin'],
        },
        {
          label: 'Failures',
          href: RoutingPaths.failures,
          displayForRoles: ['admin'],
        },
        {
          label: 'ISO Codes',
          href: RoutingPaths.isoCodes,
          displayForRoles: ['admin'],
        },
        {
          label: 'Channel Descriptions',
          href: RoutingPaths.channelDescriptions,
          displayForRoles: ['admin'],
        },
        {
          label: 'Label Descriptions',
          href: RoutingPaths.labelDescriptions,
          displayForRoles: ['admin'],
        },
        {
          label: 'Failure - Project',
          href: RoutingPaths.failuresProjects,
          displayForRoles: ['admin'],
        },
        {
          label: 'Performance Limiter Calibration',
          href: RoutingPaths.performanceLimiterCalibrations,
          displayForRoles: ['admin'],
        },
        {
          label: 'Copy failures with a list',
          href: RoutingPaths.copyFailuresList,
          displayForRoles: ['admin'],
        },
        {
          label: 'Edit Project (add or remove)',
          href: RoutingPaths.editProject,
          displayForRoles: ['admin'],
        },
        {
          label: 'Check if all labels exists in the software',
          href: RoutingPaths.checkLabelsExistence,
          displayForRoles: ['admin'],
        },
        {
          label: 'Calibration test',
          href: RoutingPaths.home, // TODO
          displayForRoles: ['admin'],
          disabled: true,
        },
      ]
    },
    {
      label: 'Users',
      href: RoutingPaths.users,
      displayForRoles: ['admin'],
    },
    // {
    //   label: 'About',
    //   href: RoutingPaths.about,
    //   displayForRoles: ['guest', 'admin'],
    //   disabled: true,
    // },
  ];

  constructor(private accountService: AccountService, private router: Router) { }

  ngOnInit() {
    this.accountService.getUserInfo()
      .subscribe(
        result => {
          this.userInfo = result;
        },
      );
  }

  logout() {
    // no need to call logout from AccountService,
    // login component will call it on the "ngOnInit" method
    this.router.navigate(['login']);
  }
}

class MenuItem {
  label: string;
  href?: string;
  subMenu?: MenuItem[];
  displayForRoles?: string[];
  disabled?: boolean;
}



