import { Component, Input, Output, EventEmitter, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { FailuresService } from '../../../services/failures.service';
import { FailureFieldMapper } from '../../../dao/failure-field-mapper';
import { Failure } from '../../../dao/failure';
import { Project } from '../../../dao/project';
import { ProjectFieldMapper } from '../../../dao/project-field-mapper';
import { ProjectsService } from '../../../services/projects.service';
import { ChannelDescriptionsService } from '../../../services/channel-descriptions.service';
import { ChannelDescriptionFieldMapper } from '../../../dao/channel-description-field-mapper';
import { ChannelDescription } from '../../../dao/channel-description';

@Component({
  selector: 'app-ambient-conditions',
  templateUrl: './ambient-conditions.component.html',
  styleUrls: ['./ambient-conditions.component.scss']
})
export class AmbientConditionsComponent implements OnInit {
  @Input() visibleIn = false;
  @Output() visibleOut: EventEmitter<boolean> = new EventEmitter<boolean>();
  @Input() failure: Failure;

  showDetailsDialog = false;
  ambientCondition: AmbientCondition;
  ambientConditionPristine: AmbientCondition;
  importFromFailure = false;
  importFromFailureOptions: any[];
  failureToImportFrom: any;

  channelDescriptionOptions: any[];
  selectedChannelDescription: ChannelDescription;

  ambientConditionsList: AmbientCondition[];
  ambientConditionsListColumns: any[] = [
    {field: 'ambCondition', header: 'Ambient Condition', width: '20%'},
    {field: 'description', header: 'Reason of Selection', width: 'calc(80% - 25px)'},
  ];

  allProjects: Project[];
  projectsList: Project[];
  checkedProjectsIds: number[];
  projectsListColumns: any[] = [
    {
      header: 'Project',
      field: this.projectFieldMapper.fieldMap['project'],
      filterMatchMode: 'contains'
    },
    {
      header: 'Version',
      field: this.projectFieldMapper.fieldMap['version'],
      filterMatchMode: 'contains'
    },
    {
      header: 'ECU',
      field: this.projectFieldMapper.fieldMap['ecu'],
      filterMatchMode: 'contains'
    },
  ];
  loadingProjectsList = false;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private dataService: FailuresService,
    private fieldMapper: FailureFieldMapper,
    private projectFieldMapper: ProjectFieldMapper,
    private projectsService: ProjectsService,
    private channelDescriptionsService: ChannelDescriptionsService,
    private channelDescriptionFieldMapper: ChannelDescriptionFieldMapper,
  ) { }

  ngOnInit(): void {
    const id = this.route.snapshot.params['id'];
    this.getAllAmbientConditions(id);
    this.projectsList = [];
    this.checkedProjectsIds = [];
    this.getAllProjects();
    this.getAllChannelDescriptionsExceptFid();
  }

  private getAllProjects() {
    this.allProjects = [];
    this.projectsService
      .getItems([], '', '', null, null)
      .subscribe(result => {
        this.allProjects = result.data;
      }
    );
  }

  private getAllChannelDescriptionsExceptFid() {
    this.channelDescriptionOptions = [];
    this.channelDescriptionsService
      .getItems(
        [{field: this.channelDescriptionFieldMapper.fieldMap.name, value: 'FId', matchMode: 'notStartsWith'}],
        this.channelDescriptionFieldMapper.fieldMap.name,
        'asc',
        null,
        null
      )
      .subscribe(result => {
        result.data.map((channelDescription: ChannelDescription) => {
          this.channelDescriptionOptions.push({value: channelDescription, label: channelDescription.name});
        });
      }
    );
  }

  private getAllAmbientConditions(failureId: number) {
    this.dataService.getAmbientConditions(failureId).subscribe(
      result => {
        this.ambientConditionsList = <AmbientCondition[]>result['ambientConditions'];
      }
    );
  }

  private close() {
    this.visibleIn = false;
    this.visibleOut.emit(false);
  }

  showDetail(ambientCondition: AmbientCondition) {
    this.projectsList = this.allProjects.filter((project: Project) => this.failure.projects.indexOf(project.id) > -1);
    for (const project of this.projectsList) {
      this.checkedProjectsIds.push(project.id);
    }

    this.getAllChannelDescriptionsExceptFid();
    this.selectedChannelDescription = null;

    this.ambientCondition = ambientCondition;
    // "clone" ambientCondition to check the "dirty" status on save
    this.ambientConditionPristine = Object.assign({}, ambientCondition);
    this.showDetailsDialog = true;
  }

  selectedChannelDescriptionChange(event) {
    this.ambientCondition.ambCondition = event.value.name;
  }

  saveAmbientConditionDetails() {
    // only save changes if form is "dirty"
    if (JSON.stringify(this.ambientCondition) !== JSON.stringify(this.ambientConditionPristine)) {

      const requestData = {
        id: this.ambientConditionPristine.id,
        failureId: this.failure.id,
        description: this.ambientCondition.description,
        comment: this.ambientCondition.comment,
        ambCondition: this.ambientCondition.ambCondition,
      };

      this.dataService.updateAmbientCondition(requestData).subscribe(
        result => {
          this.ambientConditionPristine = null;
          this.closeDetailsDialog();
        },
        error => { alert('An error has occurred!'); },
      );
    } else {
      alert('There are no changes to be saved!');
    }
  }

  private closeDetailsDialog() {
    // restore ambient condition if form is "dirty"
    if (this.ambientConditionPristine && JSON.stringify(this.ambientCondition) !== JSON.stringify(this.ambientConditionPristine)) {
      console.log('restore');
      this.ambientCondition.id = this.ambientConditionPristine.id;
      this.ambientCondition.failureId = this.ambientConditionPristine.failureId;
      this.ambientCondition.ambCondition = this.ambientConditionPristine.ambCondition;
      this.ambientCondition.description = this.ambientConditionPristine.description;
      this.ambientCondition.comment = this.ambientConditionPristine.comment;
    }

    this.showDetailsDialog = false;
    this.importFromFailure = false;
    this.projectsList = [];
    this.checkedProjectsIds = [];
    this.failureToImportFrom = null;
    this.ambientCondition = null;
    this.ambientConditionPristine = null;
  }

  // TODO: remove after implementation
  private importFromFailureChange(event) {
    console.log(event);
    alert('TODO...');
    setTimeout(() => {this.importFromFailure = false; }, 100);
  }

  private importFromFailureClick() {
    console.log(this.failureToImportFrom);
    alert('TODO...');
  }

  // projectCheckBoxChanged(projectId: number, checked: boolean) {
  updateChannelDescriptionOptions() {
    // TODO: update channelDescriptionOptions
  }
}

class AmbientCondition {
  id: number;
  failureId: number;
  ambCondition: string;
  description: string;
  comment?: string;
}



