import { Component, OnInit, ViewChildren, AfterViewInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AccountService } from '../../services/account.service';
import { isString } from 'util';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit, AfterViewInit {
  @ViewChildren('usernameInput') usernameInput;
  model: any = {};
  loading = false;
  returnUrl: string;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private accountService: AccountService,
  ) {}

  ngOnInit() {
    this.accountService.logout().subscribe(response => {});
    this.returnUrl = this.route.snapshot.queryParams['returnUrl'] || 'home';
  }

  ngAfterViewInit() {
    this.usernameInput.first.nativeElement.focus();
  }

  login() {
    this.loading = true;
    this.accountService
      .login(this.model.username, this.model.password)
      .subscribe(
        data => {
          this.loading = false;
          if (!isString(this.returnUrl)) {
            this.returnUrl = 'home';
          }
          this.router.navigate([this.returnUrl]);
        },
        error => {
          this.loading = false;
          this.usernameInput.first.nativeElement.focus();
          this.usernameInput.first.nativeElement.select();
        },
        () => {}
      );
  }
}



