import { ItemModel } from './item-model';

export class Failure implements ItemModel {
    id: number;
    name: string;
    faultTitle: string;
    failureGroupId?: number;
    comment?: string;
    cameTitle: string;
    componentId: number;
    carbComponentId: number;
    monitorStrategyDescription: string;
    timeRequired: string;
    responsibleId: number;
    isCarbRelevant: boolean;
    status?: number;
    projects?: number[];
  }




