import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { RoutingPaths } from '../../utils/routing-paths';
import { Project } from '../../dao/project';
import { ProjectsService } from '../../services/projects.service';
import { FieldDefinition } from '../../common/components/dynamic-forms/field-definition';
import { ProjectFieldMapper } from '../../dao/project-field-mapper';
import { CanComponentDeactivate } from '../../guards/can-deactivate.guard';

@Component({
  selector: 'app-project-detail',
  templateUrl: './project-detail.component.html',
  styleUrls: ['./project-detail.component.scss']
})
export class ProjectDetailComponent implements OnInit, CanComponentDeactivate {
  private isNew: boolean;
  item: Project;
  itemDefinition: Array<FieldDefinition> = [
    {
      key: this.fieldMapper.fieldMap['id'],
      label: 'Id',
      type: 'number',
      hidden: true,
      isId: true,
      required: true,
    },
    {
      key: this.fieldMapper.fieldMap['status'],
      label: '',
      type: 'label',
    },
    {
      key: this.fieldMapper.fieldMap['name'],
      label: 'Project Name',
      type: 'text',
      disabled: true,
    },
    {
      key: this.fieldMapper.fieldMap['ecu'],
      label: 'Ecu',
      type: 'text',
      required: true,
    },
    {
      key: this.fieldMapper.fieldMap['project'],
      label: 'Project',
      type: 'text',
      required: true,
    },
    {
      key: this.fieldMapper.fieldMap['version'],
      label: 'Version',
      type: 'text',
      required: true,
    },
    {
      key: this.fieldMapper.fieldMap['dsmStructure'],
      label: 'DSM Structure',
      type: 'select',
      options: [
        {value: null, label: 'Select an option'},
        {value: '0', label: '0'},
        {value: '1', label: '1'},
        {value: '2', label: '2'},
        {value: '3', label: '3'},
      ],
      selectedOptions: [''],
      placeholder: 'Select an option',
      required: true,
    },
    {
      key: this.fieldMapper.fieldMap['revision'],
      label: 'Revision',
      type: 'number',
      placeholder: 'Revision number',
      required: true,
    },
    {
      key: this.fieldMapper.fieldMap['comment'],
      label: 'Comment',
      type: 'textarea',
      placeholder: 'Comment',
    },
  ];
  errorMessage: string;
  showDialog = false;
  dialogMessage: string;
  protected lockedByCurrentUser = false;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private dataService: ProjectsService,
    private fieldMapper: ProjectFieldMapper
  ) { }

  ngOnInit() {
    this.isNew = this.route.snapshot.url[1].path === 'new';

    if (this.isNew) {
      this.lockedByCurrentUser = true;
      this.item = {
        id: 0,
        name: null,
        ecu: null,
        project: null,
        version: null,
        dsmStructure: null,
        revision: null,
        comment: null,
      };
    } else {
      const id = this.route.snapshot.params['id'];
      const statusIndex = this.itemDefinition.findIndex(item => item.key === this.fieldMapper.fieldMap['status']);
      this.dataService.lockItem(id).subscribe(
        success => {
          this.lockedByCurrentUser = true;
          this.itemDefinition[statusIndex].label = 'Locked by you';
          this.getItem();
        },
        error => {
          if (error.status === 409) { // already locked
            this.lockedByCurrentUser = false;
            this.itemDefinition[statusIndex].label = 'Locked by another user';
            this.getItem();
          }
        },
      );
    }
  }

  private getItem() {
    this.dataService.getItem(this.route.snapshot.params['id'])
      .subscribe((item: Project) => {
        this.item = item;
      }
    );
  }

  private cancel() {
    this.router.navigate([RoutingPaths.projects]);
  }

  private save(item: Project) {
    if (this.isNew) {
      this.create(item);
    } else {
      this.update(item);
    }

    this.cancel();
  }

  private update(item: Project) {
    this.dataService.editItem(item.id, item)
      .subscribe((x: any) => {
        console.log('project ' + item.id + ' updated');
      }
    );
  }

  private create(item: Project) {
    item.name = item.ecu + '_' + item.project + '_' + item.version + '_' + item.revision;
    this.dataService.addItem(item)
      .subscribe((x: any) => {
        console.log('project ' + x.id + ' created');
      }
    );
  }

  dialogKeepLock() {
    this.showDialog = false;
    this.lockedByCurrentUser = true;
    this.cancel();
  }

  dialogUnlock() {
    this.showDialog = false;
    this.dataService.unlockItem(this.route.snapshot.params['id']).subscribe(
      success => {
        this.lockedByCurrentUser = false;
        this.cancel();
      },
      error => {
        console.log('error unlocking');
      },
    );
  }

  canDeactivate(): boolean {
    if (!this.lockedByCurrentUser) {
      return true;
    }
    this.dialogMessage = 'You have locked this project, which prevents other users from editing it! What would you like to do?';
    this.showDialog = true;
  }
}



