import { Component, ViewChild } from '@angular/core';
import { Router } from '@angular/router';

import { UsersService } from '../../services/users.service';
import { User } from '../../dao/user';
import { UsersDataSource } from './users.datasource';
import { AdvancedTableData } from '../../common/components/advanced-table-data';
import { AdvancedTableComponent } from '../../common/components/advanced-table/advanced-table.component';
import { RoutingPaths } from '../../utils/routing-paths';
import { UserFieldMapper } from '../../dao/user-field-mapper';

@Component({
  selector: 'app-users',
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.scss']
})
export class UsersComponent {
  listTitle = 'Users';
  @ViewChild('table') table: AdvancedTableComponent;
  advancedTableData: AdvancedTableData<User> = {
    columns: [
      {
        description: 'Username',
        valueGetter: item => item.username,
        matColumnDefValue: this.fieldMapper.fieldMap.username,
        filterMatchMode: 'contains',
      },
      {
        description: 'Role',
        valueGetter: item => item.role,
        matColumnDefValue: this.fieldMapper.fieldMap.role,
        filterMatchMode: 'contains',
      }
    ],
    displayedColumns: [
      this.fieldMapper.fieldMap.username,
      this.fieldMapper.fieldMap.role,
    ],
    dataSource: new UsersDataSource(this.crudService),
    actions: [
      {
        icon: 'edit',
        disabled: any => false,
        event: x => this.onEdit(x),
      },
    ]
  };

  showDialog = false;
  dialogMessage: string;

  constructor(
    private router: Router,
    private crudService: UsersService,
    private fieldMapper: UserFieldMapper
  ) {}

  onEdit(row: User): void {
    const id = row.id;
    const userInfo = JSON.parse(localStorage.getItem('user'))._value;
    if (userInfo.id === id) {
      alert('You cannot edit your own role!');
    } else {
      this.router.navigate([RoutingPaths.users, id]);
    }
  }
}



