import { ItemModelFieldMapper } from './item-model-field-mapper';

export class FailureFieldMapper implements ItemModelFieldMapper {
  readonly fieldMap = {
    id: 'id',
    name: 'name',
    faultTitle: 'title',
    failureGroupId: 'failureGroup',
    comment: 'comment',
    cameTitle: 'cameTitle',
    componentId: 'componentId',
    carbComponentId: 'carbComponent',
    monitorStrategyDescription: 'monitorStrategyDescription',
    timeRequired: 'timeRequired',
    responsibleId: 'responsible',
    isCarbRelevant: 'carbrelevant',
    status: 'status',
    projects: 'projects'
  };
}



