import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { RoutingPaths } from '../../utils/routing-paths';
import { IsoCode } from '../../dao/iso-code';
import { IsoCodesService } from '../../services/iso-codes.service';
import { FieldDefinition } from '../../common/components/dynamic-forms/field-definition';
import { IsoCodeFieldMapper } from '../../dao/iso-code-field-mapper';
import { CanComponentDeactivate } from '../../guards/can-deactivate.guard';

@Component({
  selector: 'app-iso-code-detail',
  templateUrl: './iso-code-detail.component.html',
  styleUrls: ['./iso-code-detail.component.scss']
})
export class IsoCodeDetailComponent implements OnInit, CanComponentDeactivate {
  private isNew: boolean;
  item: IsoCode;
  itemDefinition: Array<FieldDefinition> = [
    {
      key: this.fieldMapper.fieldMap['id'],
      label: 'Id',
      type: 'number',
      hidden: true,
      isId: true,
      required: true,
    },
    {
      key: this.fieldMapper.fieldMap['status'],
      label: '',
      type: 'label',
    },
    {
      key: this.fieldMapper.fieldMap['code'],
      label: 'ISO Code',
      type: 'text',
      disabled: true,
    },
    {
      key: this.fieldMapper.fieldMap['pcode'],
      label: 'P Code',
      type: 'text',
      disabled: true,
    },
    {
      key: this.fieldMapper.fieldMap['description'],
      label: 'Description',
      type: 'textarea',
      required: true,
    },
    {
      key: this.fieldMapper.fieldMap['comment'],
      label: 'Comment',
      type: 'textarea',
    },
  ];
  errorMessage: string;
  showDialog = false;
  dialogMessage: string;
  protected lockedByCurrentUser = false;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private dataService: IsoCodesService,
    private fieldMapper: IsoCodeFieldMapper
  ) { }

  ngOnInit() {
    this.isNew = this.route.snapshot.url[1].path === 'new';

    if (this.isNew) {
      this.lockedByCurrentUser = true;
      this.item = {
        id: 0,
        code: null,
        pcode: null,
        description: null,
        comment: null,
      };
    } else {
      const id = this.route.snapshot.params['id'];
      const statusIndex = this.itemDefinition.findIndex(item => item.key === this.fieldMapper.fieldMap['status']);
      this.dataService.lockItem(id).subscribe(
        success => {
          this.lockedByCurrentUser = true;
          this.itemDefinition[statusIndex].label = 'Locked by you';
          this.getItem();
        },
        error => {
          if (error.status === 409) { // already locked
            this.lockedByCurrentUser = false;
            this.itemDefinition[statusIndex].label = 'Locked by another user';
            this.getItem();
          }
        },
      );
    }
  }

  private getItem() {
    this.dataService.getItem(this.route.snapshot.params['id'])
      .subscribe((item: IsoCode) => {
        this.item = item;
      }
    );
  }

  private cancel() {
    this.router.navigate([RoutingPaths.isoCodes]);
  }

  private save(item: IsoCode) {
    if (this.isNew) {
      this.create(item);
    } else {
      this.update(item);
    }

    this.cancel();
  }

  private update(item: IsoCode) {
    this.dataService.editItem(item.id, item)
      .subscribe((x: any) => {
        console.log('iso code ' + item.id + ' updated');
      }
    );
  }

  private create(item: IsoCode) {
    // TODO: ERROR (No 'Access-Control-Allow-Origin' header is present on the requested resource)
    this.dataService.addItem(item)
      .subscribe((x: any) => {
        console.log('iso code ' + x.id + ' created');
      }
    );
  }

  dialogKeepLock() {
    this.showDialog = false;
    this.lockedByCurrentUser = false;
    this.cancel();
  }

  dialogUnlock() {
    this.showDialog = false;
    this.dataService.unlockItem(this.route.snapshot.params['id']).subscribe(
      success => {
        this.lockedByCurrentUser = false;
        this.cancel();
      },
      error => {
        console.log('error unlocking');
      },
    );
  }

  canDeactivate(): boolean {
    if (!this.lockedByCurrentUser) {
      return true;
    }
    this.dialogMessage = 'You have locked this iso code, which prevents other users from editing it! What would you like to do?';
    this.showDialog = true;
  }
}



