import { BaseDataSource } from '../../common/components/base-data-source';
import { LabelDescription } from '../../dao/label-description';
import { LabelDescriptionsService } from '../../services/label-descriptions.service';
import { Observable } from 'rxjs/Observable';
import { ListResults } from '../../common/services/crud.service';
import { AdvancedTableColumnFilter } from '../../common/components/advanced-table-data';

export class LabelDescriptionsDataSource extends BaseDataSource<LabelDescription> {
    constructor(private service: LabelDescriptionsService) {
      super();
    }

    getData(
      filter: AdvancedTableColumnFilter[] = [],
      sortField: string = '',
      sortDirection: string = 'asc',
      pageIndex: number = 0,
      pageSize: number = 10
    ): Observable<ListResults<LabelDescription>> {
      return this.service.getItems(
        filter,
        sortField,
        sortDirection,
        pageIndex,
        pageSize
      );
    }
  }



