import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FormGroup, FormControl } from '@angular/forms';
import { HttpEventType, HttpResponse } from '@angular/common/http';

import { Project } from '../../dao/project';
import { ProjectFieldMapper } from '../../dao/project-field-mapper';
import { ProjectsService } from '../../services/projects.service';
import { UploadService } from '../../services/upload.service';
import { SelectOption } from '../../common/components/dynamic-forms/field-definition';
import { A2lFailureDetailFieldMapper } from '../../dao/a2l-failure-detail-field-mapper';
import { A2lFailureDetail } from '../../dao/a2l-failure-detail';
import { A2lParsingResultFieldMapper } from '../../dao/a2l-parsing-result-field-mapper';

@Component({
  selector: 'app-create',
  templateUrl: './create.component.html',
  styleUrls: ['./create.component.scss']
})
export class CreateComponent implements OnInit {

  form: FormGroup;
  selectedProject: Project;
  selectedProjectId: number;
  selectedProjectPlaceholder: string;
  projectOptions: SelectOption[];
  projectIdentifiers: Project[];
  disableSelectedProject = true;
  useA2L = true;
  selectedA2L: File;
  selectedHex: File;
  selectedComparisonFile: File;
  skipDisabledFailures = false;

  uploadPercentage = 0;
  a2lUploaded = false;
  a2lServerPath: string;
  uploadedA2lFileName: string;
  hexUploaded = false;
  hexServerPath: string;
  uploadedHexFileName: string;

  // LEGACY CODE (MD1 version)
  //
  // If datReaderDSMstr.GetValue(0) = 1 Then             // APPLIC
  //     RadBtnDTCM.Checked = True
  // Else
  //     RadBtnDTCSAE.Checked = True
  // End If
  // If datReaderDSMstr.GetValue(1) = 2 Then             // DTCstruct
  //     GrDTCview.Enabled = False
  // Else
  //     GrDTCview.Enabled = True
  // End If
  dtcDisplay = 'sae'; // TODO: get this value from the selected project
  reduceFidList = false;
  selectedCat = 0;
  catOptions = [
    { value: 0, label: 'Cat 0' },
    { value: 1, label: 'Cat 1' },
    { value: 2, label: 'Cat 2' },
  ];
  compareLabels = false;
  skipPopups = true; // does it make sense to have this here???

  showDialog = false;
  dialogShowHeader = true;
  dialogHeaderClose = false;
  dialogHeaderTitle: string;
  dialogMessage: string;
  dialogShowProgress = false;
  dialogShowProgressPercentage = false;

  parsingCompleted = false;
  csv = '';
  csvCompleted = false;

  showResultsDialog = false;
  a2lFailureDetails: A2lFailureDetail[] = [];

  showWarningsDialog = false;
  a2lWarnings: string[];

  constructor(
    private router: Router,
    private projectDataService: ProjectsService,
    private projectFieldMapper: ProjectFieldMapper,
    private uploadService: UploadService,
    private a2lFailureDetailFieldMapper: A2lFailureDetailFieldMapper,
    private a2lParsingResultFieldMapper: A2lParsingResultFieldMapper,
  ) { }

  ngOnInit() {
    this.setProjectOptions();
    this.resetForm();
  }

  resetForm() {
    this.selectedProjectPlaceholder = 'Please select an A2L file first...';
    this.form = new FormGroup({
      selectedProject: new FormControl({value: 'selectedProject', disabled: true}),
      selectedProjectId: new FormControl('selectedProjectId'),
      useA2L: new FormControl('useA2L'),
      selectedA2L: new FormControl(this.selectedA2L),
      selectedHex: new FormControl(this.selectedHex),
      skipDisabledFailures: new FormControl('skipDisabledFailures'),
      dtcDisplay: new FormControl({value: 'dtcDisplay', disabled: true}),
      compareLabels: new FormControl('compareLabels'),
      reduceFidList: new FormControl('reduceFidList'),
      selectedCat: new FormControl({value: 'selectedCat', disabled: true}),
      selectedComparisonFile: new FormControl(this.selectedComparisonFile),
      skipPopups: new FormControl('skipPopups'),
    });
  }

  selectedProjectChange(event) {
    if (event.value > 0) {
      this.selectedProjectId = event.value;
      this.setDtcByProjectId(event.value);
    }
  }

  setDtcByProjectId(projectId: number) {
    if (projectId > 0) {
      // TODO: check if the project's DTCstruct == 2, if so, disable the control
      // and check also if the project's Applic == 1, if so, check 'manufac', otherwise check 'sae'
      this.form.controls['dtcDisplay'].enable();
    } else {
      this.form.controls['dtcDisplay'].disable();
    }
  }

  onFileChange(event, whichFile: string) {
    if (event.files.length > 0) {
      this.form.get(whichFile).setValue(event.files[0]);
      if (whichFile === 'selectedA2L') {
        this.resetSelectedA2lVars();
        this.a2lFileChange(event.files[0]);
      } else {
        this.resetSelectedHexVars();
      }
    }
  }

  resetSelectedA2lVars() {
    this.a2lUploaded = false;
    this.a2lServerPath = null;
  }

  resetSelectedHexVars() {
    this.hexUploaded = false;
    this.hexServerPath = null;
  }

  onA2LFileClear(event) {
    this.selectedProject = this.selectedProjectId = null;
    this.selectedProjectPlaceholder = 'Please select an A2L file first...';
    this.setDtcByProjectId(0);
    this.form.controls['selectedProject'].disable();
    this.resetSelectedA2lVars();
  }

  onHexFileClear(event) {
    this.resetSelectedHexVars();
  }

  a2lFileChange(file: File) {
    if (file) {
      const fileReader = new FileReader();

      fileReader.onload = (e) => {
        const projectIdentifier = this.getProjectAndVersionFromA2lFile(fileReader.result.toString());

        if (this.projectIdentifiers[projectIdentifier]) {

          this.selectedProject = this.selectedProjectId = this.projectIdentifiers[projectIdentifier].id;
          this.setDtcByProjectId(this.projectIdentifiers[projectIdentifier].id);
          this.form.controls['selectedProject'].disable();

        } else if (this.projectIdentifiers[projectIdentifier.slice(-1) + 'x']) {

          this.selectedProject = this.selectedProjectId = this.projectIdentifiers[projectIdentifier.slice(-1) + 'x'].id;
          this.setDtcByProjectId(this.projectIdentifiers[projectIdentifier.slice(-1) + 'x'].id);
          this.form.controls['selectedProject'].disable();

        } else {

          this.dialogShowHeader = true;
          this.dialogMessage = 'Automatic project selection was not successful! Please select the correct project manualy.';
          this.dialogShowProgress = false;
          this.dialogShowProgressPercentage = false;
          this.showDialog = true;

          this.selectedProject = this.selectedProjectId = null;
          this.selectedProjectPlaceholder = 'Please select the correct project...';
          this.setDtcByProjectId(0);
          this.form.controls['selectedProject'].enable();

        }
      };

      fileReader.readAsText(file);
    }
  }

  formatBytes(bytes, decimals) {
    if (bytes === 0) {
      return '0 Bytes';
    }
    const k = 1024;
    const dm = decimals || 2;
    const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + ' ' + sizes[i];
  }

  uploadA2L(event) {
    if (this.a2lUploaded) {
      this.showUploadPrevented();
      return;
    }

    this.dialogShowHeader = true;
    this.dialogHeaderClose = false;
    this.dialogHeaderTitle = 'Operation in progress';
    this.dialogMessage = 'Uploading Selected A2L file to server...';
    this.dialogShowProgress = true;
    this.dialogShowProgressPercentage = true;
    this.showDialog = true;

    const formData = new FormData();
    formData.append('file', this.form.value.selectedA2L);

    this.uploadPercentage = 0;
    this.uploadService.uploadA2L(formData).subscribe(
      (result: any) => {
        if (result.type === HttpEventType.UploadProgress) {
          this.uploadPercentage = Math.round(100 * result.loaded / result.total);
        } else if (result instanceof HttpResponse) {
          if (result.body.isStoredSuccessfully) {
            this.a2lUploaded = true;
            this.a2lServerPath = result.body.path;
            this.uploadedA2lFileName = this.form.value.selectedA2L.name;
          } else {
            this.showFileStoringError();
          }
        }
      },
      error => {
        this.showFileStoringError();
        console.log(error);
      },
      () => {
        this.showDialog = false;
        this.uploadPercentage = 0;
      }
    );
  }

  uploadHex(event) {
    if (this.hexUploaded) {
      this.showUploadPrevented();
      return;
    }

    this.dialogShowHeader = true;
    this.dialogHeaderClose = false;
    this.dialogHeaderTitle = 'Operation in progress';
    this.dialogMessage = 'Uploading Selected Hex file to server...';
    this.dialogShowProgress = true;
    this.dialogShowProgressPercentage = true;
    this.showDialog = true;

    const formData = new FormData();
    formData.append('file', this.form.value.selectedHex);

    this.uploadPercentage = 0;
    this.uploadService.uploadHex(formData).subscribe(
      (result: any) => {
        if (result.type === HttpEventType.UploadProgress) {
          this.uploadPercentage = Math.round(100 * result.loaded / result.total);
        } else if (result instanceof HttpResponse) {
          if (result.body.isStoredSuccessfully) {
            this.hexUploaded = true;
            this.hexServerPath = result.body.path;
            this.uploadedHexFileName = this.form.value.selectedHex.name;
          } else {
            this.showFileStoringError();
          }
        }
      },
      error => {
        this.showFileStoringError();
        console.log(error);
      },
      () => {
        this.showDialog = false;
        this.uploadPercentage = 0;
      }
    );
  }

  useA2LChange(useA2L) {
    if (useA2L) {
      this.form.controls['selectedA2L'].enable();
    } else {
      this.form.controls['selectedA2L'].disable();
    }
  }

  reduceFidListChange(reduceFidList) {
    if (reduceFidList) {
      this.form.controls['selectedCat'].enable();
      this.dialogShowHeader = true;
      this.dialogHeaderClose = true;
      this.dialogHeaderTitle = 'Warning';
      this.dialogMessage = 'ATTENTION: Your output file will have a reduced FId List. This can cause incomplete information!';
      this.dialogShowProgress = false;
      this.dialogShowProgressPercentage = false;
      this.showDialog = true;
    } else {
      this.form.controls['selectedCat'].disable();
    }
  }

  cancel() {
    this.router.navigate(['home']);
  }

  save() {
    // const formData = new FormData();
    // formData.append('projectId', this.selectedProjectId.toString());
    // formData.append('pathA2l', this.a2lServerPath);
    // formData.append('pathHex', this.hexServerPath);

    // formData.append('useA2L', this.form.value.useA2L);
    // formData.append('fileA2L', this.form.value.selectedA2L);
    // formData.append('fileHex', this.form.value.selectedHex);
    // formData.append('skipDisabledFailures', this.form.value.skipDisabledFailures);
    // formData.append('dtcDisplay', this.form.value.dtcDisplay);
    // formData.append('reduceFidList', this.form.value.reduceFidList);
    // formData.append('selectedCat', this.form.value.selectedCat);
    // formData.append('compareLabels', this.form.value.compareLabels);
    // formData.append('fileComparison', this.form.value.selectedComparisonFile);
    // formData.append('skipPopups', this.form.value.skipPopups);

    if (this.selectedProjectId === null) {
      this.dialogShowHeader = true;
      this.dialogHeaderTitle = 'Information';
      this.dialogMessage = 'Selected EDC Project cannot be empty! Please select one.';
      this.dialogShowProgress = false;
      this.dialogShowProgressPercentage = false;
      this.showDialog = true;
      return;
    }

    const requestData = {
      ProjectId: this.selectedProjectId.toString(),
      PathA2l: this.a2lServerPath,
      PathHex: this.hexServerPath,
    };

    this.dialogShowHeader = true;
    this.dialogHeaderClose = false;
    this.dialogHeaderTitle = 'Operation in progress';
    this.dialogMessage = 'Server is parsing the information from the uploaded files...';
    this.dialogShowProgress = true;
    this.dialogShowProgressPercentage = false;
    this.showDialog = true;

    this.uploadService.parseUploadedFiles(requestData).subscribe(
      (result: any) => {
        // console.log(result);

        this.a2lWarnings = result.fnrWarnings;
        // console.log(this.a2lWarnings);

        this.a2lFailureDetails = result.fnrData.map(item => {
          const detail = item.fnrFailureDetail;
          return {
            name: detail[this.a2lFailureDetailFieldMapper.fieldMap.name],
            title: detail[this.a2lFailureDetailFieldMapper.fieldMap.title],
            dtc: detail[this.a2lFailureDetailFieldMapper.fieldMap.dtc],
            component: detail[this.a2lFailureDetailFieldMapper.fieldMap.component],
            obdSystem: detail[this.a2lFailureDetailFieldMapper.fieldMap.obdSystem],
            obdCode: detail[this.a2lFailureDetailFieldMapper.fieldMap.obdCode],
            disabled: detail[this.a2lFailureDetailFieldMapper.fieldMap.disabled],
            errorClass: detail[this.a2lFailureDetailFieldMapper.fieldMap.errorClass],
            priority: detail[this.a2lFailureDetailFieldMapper.fieldMap.priority],
            visibleScanTool: detail[this.a2lFailureDetailFieldMapper.fieldMap.visibleScanTool],
            longTermFailure: detail[this.a2lFailureDetailFieldMapper.fieldMap.longTermFailure],
            perfLim: detail[this.a2lFailureDetailFieldMapper.fieldMap.perfLim],
            systemLamp: detail[this.a2lFailureDetailFieldMapper.fieldMap.systemLamp],
            systemLampActivation: detail[this.a2lFailureDetailFieldMapper.fieldMap.systemLampActivation],
            systemLampDeactivation: detail[this.a2lFailureDetailFieldMapper.fieldMap.systemLampDeactivation],
            mil: detail[this.a2lFailureDetailFieldMapper.fieldMap.mil],
            validationMil: detail[this.a2lFailureDetailFieldMapper.fieldMap.validationMil],
            healingMil: detail[this.a2lFailureDetailFieldMapper.fieldMap.healingMil],
            dtcErasingFromMemory: detail[this.a2lFailureDetailFieldMapper.fieldMap.dtcErasingFromMemory],
            dtcErasingPending: detail[this.a2lFailureDetailFieldMapper.fieldMap.dtcErasingPending],
            dtcErasingPendingHealed: detail[this.a2lFailureDetailFieldMapper.fieldMap.dtcErasingPendingHealed],
            classification: detail[this.a2lFailureDetailFieldMapper.fieldMap.classification],
            resetBehaviour: detail[this.a2lFailureDetailFieldMapper.fieldMap.resetBehaviour],
            resetAfterrun: detail[this.a2lFailureDetailFieldMapper.fieldMap.resetAfterrun],
            faultStorage: detail[this.a2lFailureDetailFieldMapper.fieldMap.faultStorage],
            debouncingWhileTesterPresent: detail[this.a2lFailureDetailFieldMapper.fieldMap.debouncingWhileTesterPresent],
            disableBehaviour: detail[this.a2lFailureDetailFieldMapper.fieldMap.disableBehaviour],
            generalRemarks: detail[this.a2lFailureDetailFieldMapper.fieldMap.generalRemarks],
            testConditon: detail[this.a2lFailureDetailFieldMapper.fieldMap.testConditon],
            faultDetection: detail[this.a2lFailureDetailFieldMapper.fieldMap.faultDetection],
            defectDebouncing: detail[this.a2lFailureDetailFieldMapper.fieldMap.defectDebouncing],
            faultHealing: detail[this.a2lFailureDetailFieldMapper.fieldMap.faultHealing],
            healingDebouncing: detail[this.a2lFailureDetailFieldMapper.fieldMap.healingDebouncing],
            systemReaction: detail[this.a2lFailureDetailFieldMapper.fieldMap.systemReaction],
            faultDisabling: detail[this.a2lFailureDetailFieldMapper.fieldMap.faultDisabling],
            fidCalibrations: detail[this.a2lFailureDetailFieldMapper.fieldMap.fidCalibrations],
            failureCause: detail[this.a2lFailureDetailFieldMapper.fieldMap.failureCause],
            failureRepair: detail[this.a2lFailureDetailFieldMapper.fieldMap.failureRepair],
            ambientConditions: item[this.a2lParsingResultFieldMapper.fieldMap.ambientConditions] ,
          };
        });

        this.parsingCompleted = true;
        this.showDialog = false;
        this.showResultsDialog = true;
        this.showWarningsDialog = true;
      },
      error => {
        console.log(error);
        this.showDialog = false;
        this.dialogShowHeader = true;
        this.dialogHeaderClose = true;
        this.dialogHeaderTitle = 'Error';
        this.dialogMessage = 'There was an error parsing the files!';
        this.dialogShowProgress = false;
        this.dialogShowProgressPercentage = false;
        this.showDialog = true;
      }
    );
  }

  private showFileStoringError() {
    this.dialogShowHeader = true;
    this.dialogHeaderClose = true;
    this.dialogHeaderTitle = 'Error';
    this.dialogMessage = 'There was an error storing the file on server! Please re-upload it!';
    this.dialogShowProgress = false;
    this.dialogShowProgressPercentage = false;
    this.showDialog = true;
  }

  private showUploadPrevented() {
    this.dialogShowHeader = true;
    this.dialogHeaderClose = true;
    this.dialogHeaderTitle = 'Information';
    this.dialogMessage = 'This file has already been uploaded!';
    this.dialogShowProgress = false;
    this.dialogShowProgressPercentage = false;
    this.showDialog = true;
  }

  private getProjectAndVersionFromA2lFile(fileContent: string): string {
    let projectTitle = '';
    let projectVersion = '';
    const fileLines = fileContent.split(/[\r\n]+/g);

    // the following logic was extracted from the legacy tool source code (MD1 version)
    // we'll be looking for two lines to extract the project's "project" and "version" fields
    // line example to extract project: /begin PROJECT P_662 "EDC17CV41"
    // line example to extract version: VERSION "P_662_EDC17CV41_H12"
    for (let line of fileLines) {
      line = line.trim();

      if (projectTitle.length === 0 && line.startsWith('/begin PROJECT')) {
        projectTitle = this.getProjectFromA2lLine(line);

        // skip the remaining lines if projectVersion has already been extracted
        if (projectVersion.length > 0) {
          break;
        }

      } else if (projectVersion.length === 0 && line.startsWith('VERSION')) {

        if (line.indexOf('P1738') > -1) {
          projectTitle = 'P1738V';
        }

        projectVersion = this.getVersionFromA2lLine(line);

        // skip the remaining lines if projectTitle has already been extracted
        if (projectTitle.length > 0) {
          break;
        }
      }
    }

    let result = projectTitle.concat(projectVersion);

    if (result.indexOf('VV') > -1) {
      result = result.replace('VV', 'V') + '0';
    }

    return result;
  }

  private getProjectFromA2lLine(line: string): string {
    let projectTitle = line.split(' ')[2].replace('_', '') + 'V';
    if (projectTitle.length > 5) {
      projectTitle = projectTitle.substr(0, 5) + 'V';
    }
    return projectTitle;
  }

  private getVersionFromA2lLine(line: string): string {
    let projectVersion = '';
    const versionParts = line.split('_');
    for (const versionPart of versionParts) {
      if (versionPart.length < 6) {
        projectVersion += versionPart;
      }
    }

    return projectVersion.replace('"', '').slice(-3);
  }

  private setProjectOptions() {
    this.projectOptions = [];
    this.projectIdentifiers = [];
    this.projectDataService
      .getItems([], this.projectFieldMapper.fieldMap.name, 'asc', null, null)
      .subscribe(result => {
        result.data.map((project: Project) => {
          this.projectOptions.push({value: project.id, label: project.name});
          this.projectIdentifiers[project.project + project.version.substr(0, 4)] = project;
        });
      }
    );
  }
}



