import { Component, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs/Observable';

import { BaseDataSource } from '../../common/components/base-data-source';
import { AdvancedTableData } from '../../common/components/advanced-table-data';
import { ProjectsService } from '../../services/projects.service';
import { Project } from '../../dao/project';
import { ProjectsDataSource } from './projects.datasource';
import { AdvancedTableComponent } from '../../common/components/advanced-table/advanced-table.component';
import { ApisService } from '../../common/services/apis.service';
import { RoutingPaths } from '../../utils/routing-paths';
import { ProjectFieldMapper } from '../../dao/project-field-mapper';

@Component({
  selector: 'app-projects',
  templateUrl: './projects.component.html',
  styleUrls: ['./projects.component.scss']
})
export class ProjectsComponent {
  listTitle = 'Projects';
  @ViewChild('table') table: AdvancedTableComponent;
  advancedTableData: AdvancedTableData<Project> = {
    columns: [
      {
        description: 'ECU',
        valueGetter: item => item.ecu,
        matColumnDefValue: this.fieldMapper.fieldMap['ecu'],
        filterMatchMode: 'contains'
      },
      {
        description: 'Project',
        valueGetter: item => item.project,
        matColumnDefValue: this.fieldMapper.fieldMap['project'],
        filterMatchMode: 'contains'
      },
      {
        description: 'Version',
        valueGetter: item => item.version,
        matColumnDefValue: this.fieldMapper.fieldMap['version'],
        filterMatchMode: 'contains'
      },
      {
        description: 'DSM Structure',
        valueGetter: item => String(item.dsmStructure),
        matColumnDefValue: this.fieldMapper.fieldMap['dsmStructure'],
        filterMatchMode: 'in',
        // filterType: "multiSelect",
        // filterOptions: [
        //   { label: "All", value: null },
        //   { label: "One", value: 1 },
        //   { label: "Two", value: 2 },
        //   { label: "Three", value: 3 }
        // ]
      },
      {
        description: 'Revision',
        valueGetter: item => String(item.revision),
        matColumnDefValue: this.fieldMapper.fieldMap['revision'],
        filterMatchMode: 'in'
      }
    ],
    displayedColumns: [
      this.fieldMapper.fieldMap['ecu'],
      this.fieldMapper.fieldMap['project'],
      this.fieldMapper.fieldMap['version'],
      this.fieldMapper.fieldMap['dsmStructure'],
      this.fieldMapper.fieldMap['revision']
    ],
    dataSource: new ProjectsDataSource(this.crudService),
    actions: [
      {
        icon: 'edit',
        disabled: any => false,
        event: x => this.onEdit(x)
      },
      {
        icon: 'trash',
        disabled: any => false,
        event: x => this.onDelete(x)
      }
    ]
  };

  showDialog = false;
  dialogMessage: string;

  constructor(
    private router: Router,
    private crudService: ProjectsService,
    private fieldMapper: ProjectFieldMapper
  ) {}

  onEdit(row: Project): void {
    this.router.navigate([RoutingPaths.projects, row.id]);
  }

  onDelete(row: Project): void {
    this.crudService.lockItem(row.id).subscribe(
      item => {
        // if record is successfully locked => proceed to delete
        this.crudService.removeItem(row.id).subscribe(result => {
          this.table.items.splice(this.table.items.indexOf(row), 1);
        });
      },
      error => {
        if (error.status === 409) { // already locked
          this.dialogMessage = 'Record locked by another user!';
          this.showDialog = true;
        }
      }
    );
  }

  private addItem(): void {
    this.router.navigate([RoutingPaths.projects, 'new']);
  }
}



