import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { RoutingPaths } from './utils/routing-paths';
import { LoginComponent } from './pages/login/login.component';
import { HomeComponent } from './pages/home/home.component';
import { IsLoggedInGuard } from './guards/is-logged-in.guard';
import { CanDeactivateGuard } from './guards/can-deactivate.guard';
import { AboutComponent } from './pages/about/about.component';
import { ChannelDescriptionsComponent } from './pages/channel-descriptions/channel-descriptions.component';
import { ChannelDescriptionDetailComponent } from './pages/channel-description-detail/channel-description-detail.component';
import { FailuresComponent } from './pages/failures/failures.component';
import { FailureDetailComponent } from './pages/failure-detail/failure-detail.component';
import { FailuresProjectsComponent } from './pages/failures-projects/failures-projects.component';
import { IsoCodesComponent } from './pages/iso-codes/iso-codes.component';
import { IsoCodeDetailComponent } from './pages/iso-code-detail/iso-code-detail.component';
import { LabelDescriptionsComponent } from './pages/label-descriptions/label-descriptions.component';
import { LabelDescriptionDetailComponent } from './pages/label-description-detail/label-description-detail.component';
import {
  PerformanceLimiterCalibrationsComponent
} from './pages/performance-limiter-calibrations/performance-limiter-calibrations.component';
import {
  PerformanceLimiterCalibrationDetailComponent
} from './pages/performance-limiter-calibration-detail/performance-limiter-calibration-detail.component';
import { ProjectsComponent } from './pages/projects/projects.component';
import { ProjectDetailComponent } from './pages/project-detail/project-detail.component';
import { CreateComponent } from './pages/create/create.component';
import { EditProjectComponent } from './pages/edit-project/edit-project.component';
import { CheckLabelsExistenceComponent } from './pages/check-labels-existence/check-labels-existence.component';
import { CopyFailuresListComponent } from './pages/copy-failures-list/copy-failures-list.component';
import { HasAccessGuard } from './guards/has-access.guard';
import { UnauthorizedAccessComponent } from './pages/unauthorized-access/unauthorized-access.component';
import { UsersComponent } from './pages/users/users.component';
import { UserDetailComponent } from './pages/user-detail/user-detail.component';

const routes: Routes = [
  { path: '', redirectTo: RoutingPaths.home, pathMatch: 'full' },
  { path: RoutingPaths.login, component: LoginComponent },
  { path: RoutingPaths.home, component: HomeComponent, canActivate: [IsLoggedInGuard] },
  // { path: RoutingPaths.about, component: AboutComponent, canActivate: [IsLoggedInGuard] },
  { path: RoutingPaths.unauthorized, component: UnauthorizedAccessComponent/*, canActivate: [IsLoggedInGuard]*/ },

  {
    path: RoutingPaths.channelDescriptions,
    component: ChannelDescriptionsComponent,
    canActivate: [IsLoggedInGuard, HasAccessGuard],
    data: {roles: ['admin']}
  },
  {
    path: RoutingPaths.channelDescriptions + '/new',
    component: ChannelDescriptionDetailComponent,
    canActivate: [IsLoggedInGuard, HasAccessGuard], data: {roles: ['admin']}
  },
  {
    path: RoutingPaths.channelDescriptions + '/:id',
    component: ChannelDescriptionDetailComponent,
    canActivate: [IsLoggedInGuard, HasAccessGuard],
    data: {roles: ['admin']},
    canDeactivate: [CanDeactivateGuard]
  },
  {
    path: RoutingPaths.failures,
    component: FailuresComponent,
    canActivate: [IsLoggedInGuard, HasAccessGuard],
    data: {roles: ['admin']}
  },
  {
    path: RoutingPaths.failures + '/new',
    component: FailureDetailComponent,
    canActivate: [IsLoggedInGuard, HasAccessGuard],
    data: {roles: ['admin']}
  },
  {
    path: RoutingPaths.failures + '/:id',
    component: FailureDetailComponent,
    canActivate: [IsLoggedInGuard, HasAccessGuard],
    data: {roles: ['admin']},
    canDeactivate: [CanDeactivateGuard]
  },

  {
    path: RoutingPaths.isoCodes,
    component: IsoCodesComponent,
    canActivate: [IsLoggedInGuard, HasAccessGuard],
    data: {roles: ['admin']}
  },
  {
    path: RoutingPaths.isoCodes + '/new',
    component: IsoCodeDetailComponent,
    canActivate: [IsLoggedInGuard, HasAccessGuard],
    data: {roles: ['admin']}
  },
  {
    path: RoutingPaths.isoCodes + '/:id',
    component: IsoCodeDetailComponent,
    canActivate: [IsLoggedInGuard, HasAccessGuard],
    data: {roles: ['admin']},
    canDeactivate: [CanDeactivateGuard]
  },
  {
    path: RoutingPaths.labelDescriptions,
    component: LabelDescriptionsComponent,
    canActivate: [IsLoggedInGuard, HasAccessGuard],
    data: {roles: ['admin']}
  },
  {
    path: RoutingPaths.labelDescriptions + '/new',
    component: LabelDescriptionDetailComponent,
    canActivate: [IsLoggedInGuard, HasAccessGuard],
    data: {roles: ['admin']}
  },
  {
    path: RoutingPaths.labelDescriptions + '/:id',
    component: LabelDescriptionDetailComponent,
    canActivate: [IsLoggedInGuard, HasAccessGuard],
    data: {roles: ['admin']}
  },
  {
    path: RoutingPaths.projects,
    component: ProjectsComponent,
    canActivate: [IsLoggedInGuard, HasAccessGuard],
    data: {roles: ['admin']}
  },
  {
    path: RoutingPaths.projects + '/new',
    component: ProjectDetailComponent,
    canActivate: [IsLoggedInGuard, HasAccessGuard],
    data: {roles: ['admin']}
  },
  {
    path: RoutingPaths.projects + '/:id',
    component: ProjectDetailComponent,
    canActivate: [IsLoggedInGuard, HasAccessGuard],
    data: {roles: ['admin']},
    canDeactivate: [CanDeactivateGuard]
  },
  {
    path: RoutingPaths.performanceLimiterCalibrations,
    component: PerformanceLimiterCalibrationsComponent,
    canActivate: [IsLoggedInGuard, HasAccessGuard],
    data: {roles: ['admin']}
  },
  {
    path: RoutingPaths.performanceLimiterCalibrations + '/new',
    component: PerformanceLimiterCalibrationDetailComponent,
    canActivate: [IsLoggedInGuard, HasAccessGuard],
    data: {roles: ['admin']}
  },
  {
    path: RoutingPaths.performanceLimiterCalibrations + '/:id',
    component: PerformanceLimiterCalibrationDetailComponent,
    canActivate: [IsLoggedInGuard, HasAccessGuard],
    data: {roles: ['admin']},
    canDeactivate: [CanDeactivateGuard]
  },
  {
    path: RoutingPaths.copyFailuresList,
    component: CopyFailuresListComponent,
    canActivate: [IsLoggedInGuard, HasAccessGuard],
    data: {roles: ['admin']}
  },
  { path: RoutingPaths.editProject,
    component: EditProjectComponent,
    canActivate: [IsLoggedInGuard, HasAccessGuard],
    data: {roles: ['admin']}
  },
  {
    path: RoutingPaths.checkLabelsExistence,
    component: CheckLabelsExistenceComponent,
    canActivate: [IsLoggedInGuard, HasAccessGuard],
    data: {roles: ['admin']}
  },
  {
    path: RoutingPaths.users,
    component: UsersComponent,
    canActivate: [IsLoggedInGuard, HasAccessGuard],
    data: {roles: ['admin']}
  },
  {
    path: RoutingPaths.users + '/:id',
    component: UserDetailComponent,
    canActivate: [IsLoggedInGuard, HasAccessGuard],
    data: {roles: ['admin']}
  },
  {
    path: RoutingPaths.failuresProjects,
    component: FailuresProjectsComponent,
    canActivate: [IsLoggedInGuard, HasAccessGuard],
    data: {roles: ['admin']}
  },
  {
    path: RoutingPaths.create,
    component: CreateComponent,
    canActivate: [IsLoggedInGuard]
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }



