import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { FormGroup, Validators, FormControl } from '@angular/forms';

import { RoutingPaths } from '../../utils/routing-paths';
import { PerformanceLimiterCalibration } from '../../dao/performance-limiter-calibration';
import { PerformanceLimiterCalibrationService } from '../../services/performance-limiter-calibration.service';
import { FieldDefinition, SelectOption } from '../../common/components/dynamic-forms/field-definition';
import { PerformanceLimiterCalibrationFieldMapper } from '../../dao/performance-limiter-calibration-field-mapper';
import { ProjectsService } from '../../services/projects.service';
import { ProjectFieldMapper } from '../../dao/project-field-mapper';
import { Project } from '../../dao/project';
import { ChannelDescriptionsService } from '../../services/channel-descriptions.service';
import { ChannelDescriptionFieldMapper } from '../../dao/channel-description-field-mapper';
import { ChannelDescription } from '../../dao/channel-description';
import { FidReactions } from '../../dao/fid-reaction';
import { CanComponentDeactivate } from '../../guards/can-deactivate.guard';
import { isArray } from 'util';
import { ConfirmationService } from 'primeng/components/common/confirmationservice';

@Component({
  selector: 'app-performance-limiter-calibration-detail',
  templateUrl: './performance-limiter-calibration-detail.component.html',
  styleUrls: ['./performance-limiter-calibration-detail.component.scss']
})
export class PerformanceLimiterCalibrationDetailComponent implements OnInit, CanComponentDeactivate {
  private isNew: boolean;
  item: PerformanceLimiterCalibration;
  itemDefinition: Array<FieldDefinition> = [
    {
      key: this.fieldMapper.fieldMap['id'],
      label: 'Id',
      type: 'number',
      hidden: true,
      isId: true,
      required: true,
    },
    {
      key: this.fieldMapper.fieldMap['status'],
      label: '',
      type: 'label',
    },
    {
      key: this.fieldMapper.fieldMap['fid'],
      label: 'FID',
      type: 'select',
      // options will be set on ngOnInit
      selectedOptions: [],
      placeholder: 'Select FID',
      // required will be set on ngOnInit
    },
    {
      key: this.fieldMapper.fieldMap['reactionId'],
      label: 'Reaction',
      type: 'select',
      // options will be set on ngOnInit
      selectedOptions: [],
    },
  ];

  form: FormGroup;
  submitted = false;

  selectedProjects: Project[];
  selectedProjectsTemp: Project[] = [];
  selectedProjectsColumns: any[] = [
    {
      header: 'Project',
      field: this.projectFieldMapper.fieldMap['project'],
      filterMatchMode: 'contains'
    },
    {
      header: 'Version',
      field: this.projectFieldMapper.fieldMap['version'],
      filterMatchMode: 'contains'
    },
    {
      header: 'ECU',
      field: this.projectFieldMapper.fieldMap['ecu'],
      filterMatchMode: 'contains'
    },
  ];
  loadingSelectedProjects = false;

  allProjects: Project[] = [];
  projectSelectionColumns: any[] = this.selectedProjectsColumns.concat([
    {
      header: 'DSM Structure',
      field: this.projectFieldMapper.fieldMap['dsmStructure'],
      filterMatchMode: 'contains',
    },
    {
      header: 'Revision',
      field: this.projectFieldMapper.fieldMap['revision'],
      filterMatchMode: 'contains',
    },
  ]);
  loadingAllProjects = false;
  showProjectSelectionDialog = false;

  errorMessage: string;
  showDialog = false;
  dialogMessage: string;
  protected lockedByCurrentUser = false;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private dataService: PerformanceLimiterCalibrationService,
    private fieldMapper: PerformanceLimiterCalibrationFieldMapper,
    private projectsDataService: ProjectsService,
    private projectFieldMapper: ProjectFieldMapper,
    private channelDescriptionsDataService: ChannelDescriptionsService,
    private channelDescriptionFieldMapper: ChannelDescriptionFieldMapper,
    private confirmationService: ConfirmationService
  ) { }

  ngOnInit() {
    this.selectedProjects = [];

    // get options for reaction dropdown
    const reactionOptions: SelectOption[] = [{value: 0, label: 'Select an option'}];
    FidReactions.fidReactions.map(fidReaction => {
      reactionOptions.push({value: fidReaction.id, label: fidReaction.name});
    });
    const reactionFieldDefinitionIdx = this.itemDefinition.findIndex(item => item.key === this.fieldMapper.fieldMap['reactionId']);
    this.itemDefinition[reactionFieldDefinitionIdx].options = reactionOptions;

    // get options for fid dropdown
    // in legacy app, the fid dropdown displays all the channel descriptions that start with "FId_"
    const fidOptions = [];
    this.channelDescriptionsDataService
      .getItems(
        [{field: this.channelDescriptionFieldMapper.fieldMap.name, value: 'FId_', matchMode: 'startsWith'}],
        this.channelDescriptionFieldMapper.fieldMap.name,
        'asc',
        null,
        null
      )
      .subscribe(result => {
        result.data.map((channelDescription: ChannelDescription) => {
          fidOptions.push({value: channelDescription.name, label: channelDescription.name});
        });

        const fidFieldDefinitionIdx = this.itemDefinition.findIndex(item => item.key === this.fieldMapper.fieldMap['fid']);
        this.itemDefinition[fidFieldDefinitionIdx].options = fidOptions;
        this.itemDefinition[fidFieldDefinitionIdx].required = true;
      }
    );

    // get records for projects dialog
    this.projectsDataService
      .getItems([], '', '', null, null)
      .subscribe(result => {
        this.allProjects = result.data;
      }
    );

    this.isNew = this.route.snapshot.url[1].path === 'new';

    if (this.isNew) {
      this.lockedByCurrentUser = true;
      this.item = {
        id: 0,
        fid: null,
        reactionId: null,
        projects: null,
      };
    } else {
      const id = this.route.snapshot.params['id'];
      const statusIndex = this.itemDefinition.findIndex(item => item.key === this.fieldMapper.fieldMap['status']);
      this.dataService.lockItem(id).subscribe(
        success => {
          this.lockedByCurrentUser = true;
          this.itemDefinition[statusIndex].label = 'Locked by you';
        },
        error => {
          if (error.status === 409) { // already locked
            this.lockedByCurrentUser = false;
            this.itemDefinition[statusIndex].label = 'Locked by another user';
          }
        },
        () => {
          this.getItem();
        },
      );
    }

    this.resetForm();
  }

  resetForm() {
    const group = {};
    this.itemDefinition.forEach(field => {
      group[field.key] = field.required ?
        new FormControl({value: field, disabled: field.disabled}, Validators.required) :
        new FormControl({value: field, disabled: field.disabled});
    });
    this.form = new FormGroup(group);
  }

  private getItem() {
    this.dataService.getItem(this.route.snapshot.params['id'])
      .subscribe((item: PerformanceLimiterCalibration) => {
        // let projectFieldDefinitionIdx = this.itemDefinition.findIndex(item => item.key === "projects");
        // //the following map is needed because the api returns an array of Projects
        // this.itemDefinition[projectFieldDefinitionIdx].selectedOptions = item.projects.map((project: Project) => project.id.toString());

        const fidFieldDefinitionIdx = this.itemDefinition.findIndex(itemDef => itemDef.key === 'fid');
        this.itemDefinition[fidFieldDefinitionIdx].selectedOptions = [item.fid];

        const reactionFieldDefinitionIdx = this.itemDefinition.findIndex(itemDef => itemDef.key === 'reactionId');
        this.itemDefinition[reactionFieldDefinitionIdx].selectedOptions = [item.reactionId.toString()];

        this.item = item;
        this.selectedProjects = item.projects;
      }
    );
  }

  private cancel(bypass: boolean = false) {
    if (bypass || this.form.pristine) {
      this.errorMessage = null;
      this.router.navigate([RoutingPaths.performanceLimiterCalibrations]);
    } else {
      this.confirmationService.confirm({
        message: 'Unsaved changes will be lost! Are you sure that you want to proceed?',
        header: 'Confirmation',
        icon: 'fa fa-question-circle',
        accept: () => {
          this.errorMessage = null;
          this.router.navigate([RoutingPaths.performanceLimiterCalibrations]);
        }
      });
    }
  }

  private save() {
    const itemCopy = Object.assign({}, this.item);
    itemCopy.fid = isArray(this.form.value.fid) ? this.form.value.fid[0] : this.form.value.fid;
    itemCopy.reactionId = isArray(this.form.value.reactionId) ? this.form.value.reactionId[0] : this.form.value.reactionId;
    itemCopy.projects = this.selectedProjects.map((project: Project) => project.id);

    this.submitted = true;
    if (this.isNew) {
      this.create(itemCopy);
    } else {
      this.update(itemCopy);
    }

    this.cancel(true);
  }

  private update(item: PerformanceLimiterCalibration) {
    this.dataService.editItem(item.id, item)
      .subscribe((x: any) => {
        console.log('performance limiter calibration ' + item.id + ' updated');
      }
    );
  }

  private create(item: PerformanceLimiterCalibration) {
    this.dataService.addItem(item)
      .subscribe((x: any) => {
        console.log('performance limiter calibration ' + x.id + ' created');
      }
    );
  }

  dialogKeepLock() {
    this.showDialog = false;
    this.lockedByCurrentUser = false;
    this.cancel(true);
  }

  dialogUnlock() {
    this.showDialog = false;
    this.dataService.unlockItem(this.route.snapshot.params['id']).subscribe(
      success => {
        this.lockedByCurrentUser = false;
        this.cancel(true);
      },
      error => {
        console.log('error unlocking');
      },
    );
  }

  canDeactivate(): boolean {
    if (!this.lockedByCurrentUser) {
      return true;
    }
    this.dialogMessage = 'You have locked this performance limiter calibration, '
      + 'which prevents other users from editing it! What would you like to do?';
    this.showDialog = true;
  }

  unselectProject(project: Project) {
    const projectIdx = this.selectedProjects.findIndex(proj => proj.id === project.id);
    if (projectIdx > -1) {
      this.selectedProjects.splice(projectIdx, 1);
    }
  }

  private openProjectSelectionDialog() {
    this.selectedProjectsTemp = this.selectedProjects;
    this.showProjectSelectionDialog = true;
  }

  private closeProjectSelectionDialog() {
    this.showProjectSelectionDialog = false;
    this.selectedProjectsTemp = [];
  }

  private saveProjectSelectionDialog() {
    this.selectedProjects = this.selectedProjectsTemp;
    this.closeProjectSelectionDialog();
  }
}



